<style scoped>
.map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.map_select {
    position: absolute;
    top: 10px;
    left: 10px;
}
</style>
<template>
    <div>
        <baidu-map class="map" :center="center" :zoom="zoom" scroll-wheel-zoom="true" @ready="handler" :map-click="false" :style="{height: webHeight + 'px'}">
            <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
            <bm-marker v-for="(point,index) in points" :position="point" @click="show(index,points)" title="吉林大学南岭校区-六餐厅" :icon="{url: point.status == 0 ? img0 : point.status == 1 ? img1 : point.status == 2 ? img2 : img3,size:{width:20,height:30}}" zIndex="100000" key="point.termno"></bm-marker>
        </baidu-map>
        <Select v-model="selectNow" style="width:150px" @on-change="selectChange" class="map_select">
            <Option v-for="item in centers" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
import BaiduMap from 'vue-baidu-map/components/Map/Map.vue'
export default {
    data() {
        return {
            spinShow: false,
            center: {lng: 0, lat: 0},
            zoom: 18,
            webHeight: window.innerHeight - 122,
            points: [],
            centers: [],
            selectNow: '0',
            img0: require('../images/dot0.png'),
            img1: require('../images/dot1.png'),
            img2: require('../images/dot2.png'),
            img3: require('../images/dot3.png')
        }
    },
    components: {
        BaiduMap
    },
    mounted() {
        // this.$ajax.get("returnMap.do")
        //     .then(res => {
        //         this.centers = res.data.campusdata
        //         this.points = res.data.terminaldata
        //         this.spinShow = false
        //     })
        this.$ajax.get("static/MapCenters.json")
            .then(res => {
                this.centers = res.data
            })
        this.$ajax.get("static/MapPoints.json")
            .then(res => {
                this.points = res.data
                this.spinShow = false
            })
    },
    methods: {
        handler ({BMap, map}) {
            this.center.lng = 125.339968
            this.center.lat = 43.863246
            this.zoom = 17
        },
        show (index,n) {
            this.$Modal.info({
                title: `${n[index].title}`,
                content: `监控编号：${n[index].termno}<br>终端类型：${n[index].termtypename}<br>地理区域：${n[index].graphyname}<br>IP　地址：${n[index].termip}<br>服务端口：${n[index].termport}<br>异常信息：${n[index].message}<br>发生时间：${n[index].exceptionstart}<br>扫描时间：${n[index].rollcalltime}<br>`
            })
        },
        selectChange () {
            this.center.lng = this.centers[this.selectNow].lng
            this.center.lat = this.centers[this.selectNow].lat
        }
    }
}
</script>


