<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="margin-left: 8px;float: right;">添加</i-button>
            <span>姓名：</span>
            <i-input v-model="nameValue" placeholder="请输入姓名" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="联系人表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="280"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <FormItem label="姓名：" prop="CONTACTNAME">
                    <Input v-model="formData.CONTACTNAME" placeholder="请输入姓名" />
                </FormItem>
                <FormItem label="电话：" prop="CONTACTPHONE">
                    <Input v-model="formData.CONTACTPHONE" placeholder="请输入电话号" />
                </FormItem>
                <FormItem label="手机：" prop="CONTACTMOBILE">
                    <Input v-model="formData.CONTACTMOBILE" placeholder="请输入手机号" />
                </FormItem>
                <FormItem label="邮箱：" prop="CONTACTEMAIL">
                    <Input v-model="formData.CONTACTEMAIL" placeholder="请输入邮箱" />
                </FormItem>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-if="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-if="!addShow">提交</Button>
            </div>
        </Drawer> 
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        const validateTel = (rule, value, callback) => {
            const reg = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的电话号'))
            } else {
                callback()
            }
        }
        const validatePhone = (rule, value, callback) => {
            const reg = /^1[34578]\d{9}$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的手机号'))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                CONTACTCODE: '',
                CONTACTNAME: '',
                CONTACTPHONE: '',
                CONTACTMOBILE: '',
                CONTACTEMAIL: ''
            },
            ruleValidate: {
                CONTACTNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                CONTACTPHONE: [
                    { validator: validateTel, trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ],
                CONTACTMOBILE: [
                    { validator: validatePhone, trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ],
                CONTACTEMAIL: [
                    { type: 'email', message: '请输入正确邮箱', trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '姓名',
                    key: 'CONTACTNAME'
                },
                {
                    title: '电话',
                    key: 'CONTACTPHONE'
                },
                {
                    title: '手机',
                    key: 'CONTACTMOBILE'
                },
                {
                    title: '邮箱',
                    key: 'CONTACTEMAIL'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改'),
                            h('i-button',{
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.removeConfirm(params.index)
                                    }
                                }
                            },'删除')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.CONTACTNAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            this.drawerShow = true
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("addContactNew.do",{
                            params: {
                                CONTACTNAME: this.formData.CONTACTNAME,
                                CONTACTPHONE: this.formData.CONTACTPHONE,
                                CONTACTMOBILE: this.formData.CONTACTMOBILE,
                                CONTACTEMAIL: this.formData.CONTACTEMAIL
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listContactNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editContactNew.do",{
                            params: {
                                CONTACTCODE: this.formData.CONTACTCODE,
                                CONTACTNAME: this.formData.CONTACTNAME,
                                CONTACTPHONE: this.formData.CONTACTPHONE,
                                CONTACTMOBILE: this.formData.CONTACTMOBILE,
                                CONTACTEMAIL: this.formData.CONTACTEMAIL
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listContactNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.CONTACTCODE = this.showList[index].CONTACTCODE
        },
        remove (index) {
            this.spinShow = false
            this.delShow = false
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].CONTACTNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        // this.$ajax.get("listContactNew.do")
        this.$ajax.get("static/Contacts.json")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



