<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="margin-left: 8px;float: right;">添加</i-button>
            <span>终端名称：</span>
            <i-input v-model="nameValue" placeholder="请输入终端名称" style="width: 150px;margin-right: 8px;"></i-input>
            <span>终端类型：</span>
            <Select v-model="typeValue" placeholder="请选择终端类型" style="width: 150px;margin-right: 8px;">
                <i-option value="%" :key="typeL">全部</i-option>
                <i-option v-for="item in typeList" :value="item.TERMTYPECODE" :key="typeL">{{ item.TERMTYPENAME }}</i-option>
            </Select>
            <span>地理区域：</span>
            <Select v-model="areaValue" placeholder="请选择地理区域" style="width: 150px;margin-right: 8px;">
                <i-option value="%" :key="typeL">全部</i-option>
                <i-option v-for="item in areaList" :value="item.GRAPHYNO" :key="areaL">{{ item.GRAPHYNAME }}</i-option>
            </Select>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="终端信息表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="1120"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="6">
                        <FormItem label="名称：" prop="TERMNAME">
                            <Input v-model="formData.TERMNAME" placeholder="请输入名称" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="终端类型：" prop="TERMTYPECODE">
                            <Select v-model="formData.TERMTYPECODE" placeholder="请选择终端类型" @on-change="typeChange" clearable>
                                <i-option v-for="item in typeList" :value="item.TERMTYPECODE" :key="typeL">{{ item.TERMTYPENAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="节点编号：" prop="NODENUM">
                            <Input v-model="formData.NODENUM" placeholder="请输入节点编号" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="地理区域：" prop="GRAPHYNO">
                            <Select v-model="formData.GRAPHYNO" placeholder="请选择地理区域" clearable>
                                <i-option v-for="item in areaList" :value="item.GRAPHYNO" :key="areaL">{{ item.GRAPHYNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="6">
                        <FormItem label="经营单位：" prop="UNITNO">
                            <Select v-model="formData.UNITNO" placeholder="请选择经营单位" clearable>
                                <i-option v-for="item in unitList" :value="item.UNITNO" :key="unitL">{{ item.UNITNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="上级终端：" prop="PARENTTERM">
                            <Select v-model="formData.PARENTTERM" placeholder="请选择上级终端" not-found-text="终端类型无对应上级终端" clearable>
                                <i-option v-for="item in parentList" :value="item.TERMNO" :key="parentL">{{ item.TERMNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="设备编号：" prop="DEVICECODE">
                            <Input v-model="formData.DEVICECODE" placeholder="请输入设备编号" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="资产编号：" prop="ASSETCODE">
                            <Input v-model="formData.ASSETCODE" placeholder="请输入资产编号" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="6">
                        <FormItem label="是否监控：" prop="INUSEFLAG">
                            <RadioGroup v-model="formData.INUSEFLAG">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="是否子终端监控：" prop="CONFIRM_PARENT_INUSEFLAG" v-if="YesNoParent">
                            <RadioGroup v-model="formData.CONFIRM_PARENT_INUSEFLAG">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                                <Radio value="2" label="不变"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="是否支持VNC远程管理：" prop="VNCSUPPORT">
                            <RadioGroup v-model="formData.VNCSUPPORT" @on-change="useVncA">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="VNC登录需密码：" prop="INPUTVNCPWD" v-if="useVnc">
                            <RadioGroup v-model="formData.INPUTVNCPWD" @on-change="useVncP">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16" v-if="useVnc">
                    <Col span="6">
                        <FormItem label="VNC端口：" prop="VNCPORT">
                            <Input v-model="formData.VNCPORT" placeholder="VNC端口" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="VNC登录密码：" prop="VNCPASSWORD" v-if="useVncPassword">
                            <Input type="password" v-model="formData.VNCPASSWORD" placeholder="VNC登录密码" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="6">
                        <FormItem label="终端IP：" prop="TERMIP">
                            <Input v-model="formData.TERMIP" placeholder="请输入终端IP" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="服务端口：" prop="TERMPORT">
                            <Input v-model="formData.TERMPORT" placeholder="请输入服务端口" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="PSAM卡编号：" prop="PSAMCODE">
                            <Input v-model="formData.PSAMCODE" placeholder="请输入PSAM卡编号" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="操作系统：" prop="TERMOSINFO">
                            <Input v-model="formData.TERMOSINFO" placeholder="请输入操作系统名称" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">  
                    <Col span="6">
                        <FormItem label="数据库：" prop="TERMDBINFO" >
                            <Input v-model="formData.TERMDBINFO" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="X坐标：" prop="GRAPHYX">
                            <Input v-model="formData.GRAPHYX" placeholder="请输入X坐标" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="Y坐标：" prop="GRAPHYY">
                            <Input v-model="formData.GRAPHYY" placeholder="请输入Y坐标" />
                        </FormItem>
                    </Col>
                    <Col span="6">
                        <FormItem label="备注：" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 1,maxRows: 1}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-show="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-show="!addShow">提交</Button>
            </div>
        </Drawer>
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        const validateGraphy = (rule, value, callback) => {
            const reg = /^([1-9]+(\.\d+)?|0\.\d+)$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的坐标'))
            } else {
                callback()
            }
        }
        return {
            YesNoParent: false,
            areaValue: '%',
            typeValue: '%',
            useVnc: false,
            useVncPassword: false,
            typeList: [],
            unitList: [],
            areaList: [],
            parentList: [],
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            contactsList: [],
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                TERMNO: '',
                TERMNAME: '',
                TERMTYPECODE: '',
                NODENUM: '',
                GRAPHYNO: '',
                UNITNO: '',
                PARENTTERM: '',
                DEVICECODE: '',
                ASSETCODE: '',
                PSAMCODE: '',
                TERMOSINFO: '',
                TERMDBINFO: '',
                TERMIP: '',
                TERMPORT: '',
                INUSEFLAG: '',
                VNCSUPPORT: '',
                VNCPORT: '',
                INPUTVNCPWD: '',
                VNCPASSWORD: '',
                CONFIRM_PARENT_INUSEFLAG: '',
                GRAPHYX: '',
                GRAPHYY: '',
                MEMO: ''
            },
            ruleValidate: {
                TERMNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' }
                ],
                TERMTYPECODE: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                GRAPHYNO: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                UNITNO: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                NODENUM: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                NETMANAGERNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                DBUSER: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                DBPASSWD: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                EMPORT: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 10,message: '不能大于10个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                GRAPHYX: [
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' },
                    { validator: validateGraphy, trigger: 'blur' }
                ],
                GRAPHYY: [
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' },
                    { validator: validateGraphy, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '监控编号',
                    key: 'TERMNO'
                },
                {
                    title: '名称',
                    key: 'TERMNAME'
                },
                {
                    title: '终端类型',
                    key: 'TERMTYPENAME'
                },
                {
                    title: '经营单位',
                    key: 'UNITNAME'
                },
                {
                    title: '地理区域',
                    key: 'GRAPHYNAME'
                },
                {
                    title: '终端IP',
                    key: 'TERMIP'
                },
                {
                    title: '上级终端',
                    key: 'PARENTNAME'
                },
                {
                    title: '是否监控',
                    key: 'INUSEFLAG',
                    render (h, params) {
                        return h('div',params.row.INUSEFLAG == 1 ? '是' : '否')
                    }
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改'),
                            h('i-button',{
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.removeConfirm(params.index)
                                    }
                                }
                            },'删除')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
            this.formData.INUSEFLAG = '是'
            this.formData.VNCSUPPORT = '否'
            this.formData.INPUTVNCPWD = '否'
            this.YesNoParent = true
            this.formDataCONFIRM_PARENT_INUSEFLAG = '不变'
            this.useVncA()
            this.useVncP()
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.TERMNAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            if (_showList.PARENTTERM == -1) {
                this.YesNoParent = true
            } else {
                this.YesNoParent = false
            }
            _formData.CONFIRM_PARENT_INUSEFLAG = '不变'
            _formData.INUSEFLAG = _showList.INUSEFLAG == 1 ? '是' : '否'
            _formData.VNCSUPPORT = _showList.VNCSUPPORT == 1 ? '是' : '否'
            _formData.INPUTVNCPWD = _showList.INPUTVNCPWD == 1 ? '是' : '否'
            _formData.VNCSUPPORT == '是' ? this.useVnc = true : this.useVnc = false
            _formData.INPUTVNCPWD == '是' ? this.useVncPassword = true : this.useVncPassword = false
            _formData.VNCPASSWORD = ''
            let tpcode = 25
            switch(_showList.TERMTYPECODE){
                case 2:
                    tpcode = 1
                    break
                case 6:
                    tpcode = 5
                    break
                case 7:
                    tpcode = 6
                    break
                case 9:
                    tpcode = 25
                    break
                case 24:
                    tpcode = 11
                    break
                case 26:
                    tpcode = 27
                    break
                case 64:
                    tpcode = 31
                    break
                case 65:
                    tpcode = 31
                    break
                case 66:
                    tpcode = 31
                    break
                case 67:
                    tpcode = 31
                    break
                case 68:
                    tpcode = 31
                    break
                case 69:
                    tpcode = 31
                    break
                case 364:
                    tpcode = 31
                    break
                case 365:
                    tpcode = 31
                    break
                case 366:
                    tpcode = 31
                    break
                case 368:
                    tpcode = 31
                    break
                case 369:
                    tpcode = 31
                    break
                case 464:
                    tpcode = 31
                    break
                case 465:
                    tpcode = 31
                    break
                case 466:
                    tpcode = 31
                    break
                case 468:
                    tpcode = 31
                    break
                case 469:
                    tpcode = 31
                    break
                default:
                    tpcode = 25
                    break
            }
            this.$ajax.get("getTerminalListNew.do",{
                params: {
                    TERMTYPECODE: tpcode
                }
            })
            .then(res => {
                this.parentList = res.data
                this.drawerShow = true
            }) 
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.drawerShow = false
                    this.$ajax.get("addTerminalNew.do",{
                            params: {
                                TERMNAME: this.formData.TERMNAME,
                                TERMTYPECODE: this.formData.TERMTYPECODE,
                                NODENUM: this.formData.NODENUM,
                                GRAPHYNO: this.formData.GRAPHYNO,
                                UNITNO: this.formData.UNITNO,
                                PARENTTERM: this.formData.PARENTTERM,
                                DEVICECODE: this.formData.DEVICECODE,
                                ASSETCODE: this.formData.ASSETCODE,
                                PSAMCODE: this.formData.PSAMCODE,
                                TERMOSINFO: this.formData.TERMOSINFO,
                                TERMDBINFO: this.formData.TERMDBINFO,
                                TERMIP: this.formData.TERMIP,
                                TERMPORT: this.formData.TERMPORT,
                                INUSEFLAG: this.formData.INUSEFLAG == '是' ? 1 : 0,
                                CONFIRM_PARENT_INUSEFLAG: this.formData.CONFIRM_PARENT_INUSEFLAG == '是' ? 1 : this.formData.CONFIRM_PARENT_INUSEFLAG == '否' ? 0 : 2,
                                VNCSUPPORT: this.formData.VNCSUPPORT == '是' ? 1 : 0,
                                VNCPORT: this.formData.VNCPORT,
                                INPUTVNCPWD: this.formData.INPUTVNCPWD == '是' ? 1 : 0,
                                VNCPASSWORD: this.formData.VNCPASSWORD,
                                GRAPHYX: this.formData.GRAPHYX,
                                GRAPHYY: this.formData.GRAPHYY,
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listTerminalNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.drawerShow = false
                    this.$ajax.get("editTerminalNew.do",{
                            params: {
                                TERMNO: this.formData.TERMNO,
                                TERMNAME: this.formData.TERMNAME,
                                TERMTYPECODE: this.formData.TERMTYPECODE,
                                NODENUM: this.formData.NODENUM,
                                GRAPHYNO: this.formData.GRAPHYNO,
                                UNITNO: this.formData.UNITNO,
                                PARENTTERM: this.formData.PARENTTERM,
                                DEVICECODE: this.formData.DEVICECODE,
                                ASSETCODE: this.formData.ASSETCODE,
                                PSAMCODE: this.formData.PSAMCODE,
                                TERMOSINFO: this.formData.TERMOSINFO,
                                TERMDBINFO: this.formData.TERMDBINFO,
                                TERMIP: this.formData.TERMIP,
                                TERMPORT: this.formData.TERMPORT,
                                INUSEFLAG: this.formData.INUSEFLAG == '是' ? 1 : 0,
                                CONFIRM_PARENT_INUSEFLAG: this.formData.CONFIRM_PARENT_INUSEFLAG == '是' ? 1 : this.formData.CONFIRM_PARENT_INUSEFLAG == '否' ? 0 : 2,
                                VNCSUPPORT: this.formData.VNCSUPPORT == '是' ? 1 : 0,
                                VNCPORT: this.formData.VNCPORT,
                                INPUTVNCPWD: this.formData.INPUTVNCPWD == '是' ? 1 : 0,
                                VNCPASSWORD: this.formData.VNCPASSWORD,
                                GRAPHYX: this.formData.GRAPHYX,
                                GRAPHYY: this.formData.GRAPHYY,
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listTerminalNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.TERMNO = this.showList[index].TERMNO
        },
        remove () {
            this.delShow = false
            this.$ajax.get("removeTerminalNew.do",{
                    params: {
                        TERMNO: this.formData.TERMNO
                    }
                })
                .then(res => {
                    if (res.data.success == 'true') {
                        this.$Message.success('删除成功')
                        this.spinShow = true
                        this.$ajax.get("listTerminalNew.do")
                            .then(res => {
                                this.totalList = res.data
                                this.searchList = this.totalList
                                this.tableLoad()
                                this.spinShow = false
                            })
                    } else {
                        this.$Message.error(res.data.msg)
                    }
                })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            if (this.typeValue != '%') {
                this.searchType()
            }
            if (this.areaValue != '%') {
                this.searchArea()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchType () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMTYPECODE == this.typeValue) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchArea () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].GRAPHYNO == this.areaValue) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        typeChange (typecode) {
            this.formData.PARENTTERM = ''
            let tpcode = '25'
            switch(typecode){
                case '2':
                    tpcode = '1'
                    break
                case '6':
                    tpcode = '5'
                    break
                case '7':
                    tpcode = '6'
                    break
                case '9':
                    tpcode = '25'
                    break
                case '24':
                    tpcode = '11'
                    break
                case '26':
                    tpcode = '27'
                    break
                case '64':
                    tpcode = '31'
                    break
                case '65':
                    tpcode = '31'
                    break
                case '66':
                    tpcode = '31'
                    break
                case '67':
                    tpcode = '31'
                    break
                case '68':
                    tpcode = '31'
                    break
                case '69':
                    tpcode = '31'
                    break
                case '364':
                    tpcode = '31'
                    break
                case '365':
                    tpcode = '31'
                    break
                case '366':
                    tpcode = '31'
                    break
                case '368':
                    tpcode = '31'
                    break
                case '369':
                    tpcode = '31'
                    break
                case '464':
                    tpcode = '31'
                    break
                case '465':
                    tpcode = '31'
                    break
                case '466':
                    tpcode = '31'
                    break
                case '468':
                    tpcode = '31'
                    break
                case '469':
                    tpcode = '31'
                    break
                default:
                    tpcode = '25'
                    break
            }
            this.$ajax.get("getTerminalListNew.do",{
                params: {
                    TERMTYPECODE: tpcode
                }
            })
            .then(res => {
                this.parentList = res.data
            })
        },
        useVncA () {
            this.formData.VNCSUPPORT == '是' ? this.useVnc = true : this.useVnc = false
            this.formData.INPUTVNCPWD = '否'
            this.useVncPassword = false
            this.formData.VNCPASSWORD = ''
            this.formData.VNCPORT = ''
        },
        useVncP () {
            this.formData.INPUTVNCPWD == '是' ? this.useVncPassword = true : this.useVncPassword = false
            this.formData.VNCPASSWORD = ''
        }
    },
    mounted() {
        this.$ajax.get("listTerminalNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
        this.$ajax.get("getTermTypeListNew.do")
            .then(res => {
                this.typeList = res.data
            })
        this.$ajax.get("getGraphyListNew.do")
            .then(res => {
                this.areaList = res.data
            })
        this.$ajax.get("getTermUnitListNew.do")
            .then(res => {
                this.unitList = res.data
            })
    }
}
</script>



