                    </div>
                </TabPane>
                <TabPane label="服务资源占比" icon="md-stats" class=" pop-big" v-if="popResource" name="key1">
                    <i-table border stripe :columns="columnsResource" :data="popDetail.resource"></i-table>
                </TabPane>
                <TabPane label="状态和版本" icon="md-filing" class="pop-big" v-if="popEdition" name="key2">
                    <i-table border stripe :columns="columnsEdition" :data="popDetail.edition"></i-table>
                </TabPane>
                <TabPane label="消息队列情况" icon="md-list" class="pop-big" v-if="popInfo" name="key3">
                    <i-table border stripe :columns="columnsInfo" :data="popDetail.info"></i-table>
