                <TabPane label="子系统连接情况" icon="md-list" class="pop-big" v-if="popSubsystem" name="key4">
                    <i-table border stripe :columns="columnsSubsystem" :data="popDetail.subsystem"></i-table>
                </TabPane>
                <TabPane label="Oracle数据库监控信息" icon="md-list" class="pop-detail pop-oracle pop-big" v-if="popOracle" name="key5">
