                        </Col>
                        <Col span="8">
                            <label>共享池容量：</label><input :value="popDetail.oracle.SHAREPOOLSIZE" readonly />
                        </Col>
                        <Col span="8">
                            <label>共享池使用率：</label><input :value="popDetail.oracle.SPUSEDPENCENT" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label>SGA命中率：</label><input :value="popDetail.oracle.SGAHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA共享缓冲区命中率：</label><input :value="popDetail.oracle.LIBRARYCACHEHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA字典缓冲区命中率：</label><input :value="popDetail.oracle.ROWCACHEHITRATIO" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
