                        </Col>
                        <Col span="8">
                            <label>状态更新时间：</label><input :value="popDetail.oracle.UPDATETIME" readonly />
                        </Col>
                    </Row>
                </TabPane>
            </Tabs>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
            </div>
        </Drawer>
		<Spin fix v-if="spinShow">
	        <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
	        <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
	    </Spin>
	</div>
</template>
<script>
export default {
	data () {
		return {
            abnormalList: [],
            tabKey: 'key0',
			popDetail: {detail: {},oracle: {}},
            drawerShow: false,
            popResource: false,
            popEdition: false,
            popSubsystem: false,
            popOracle: false,
            popInfo: false,
            spinShow: true,
			services: [],
			systems: [],
			equipments: [],
			others: [],
			textShow: false,
			showOthersBtn: true,
            columnsResource: [
                {
                    title: '进程名',
                    key: 'key_0'
                },
                {
                    title: '进程号',
                    key: 'key_1'
                },
                {
                    title: '父进程号',
                    key: 'key_2'
                },
                {
                    title: 'CPU使用率',
                    key: 'key_3'
                },
                {
                    title: '内存使用率',
                    key: 'key_4'
                },
                {
                    title: '用户',
                    key: 'key_5'
                }
            ],
            columnsEdition: [
                {
                    title: '进程名',
                    key: 'key_0'
                },
                {
                    title: '状态',
                    key: 'key_1'
                },
                {
                    title: '端口',
                    key: 'key_2'
                },
                {
                    title: '版本',
                    key: 'key_3'
                }
            ],
            columnsInfo: [
                {
                    title: '键值',
                    key: 'key_0'
                },
                {
                    title: '队列 ID',
                    key: 'key_1'
                },
                {
                    title: '用户名',
                    key: 'key_2'
                },
                {
                    title: '权限',
                    key: 'key_3'
                },
                {
                    title: '已用字节数',
                    key: 'key_4'
                },
                {
                    title: '消息队列数',
                    key: 'key_5'
                },
            ],
            columnsSubsystem: [
                {
                    title: '子系统名称',
                    key: 'key_0'
                },
                {
                    title: '是否连接',
                    key: 'key_1'
                }
            ]
		}
	},
	methods: {
		show (index,type,number) {
            const _this = this
            _this.spinShow = true
            _this.$ajax.get("static/json.json",{
                params: {
                    no: number
                }
            })
            .then(res => {
                _this.popDetail = res.data
                _this.tabKey = 'key0'
                const popShow = document.getElementsByClassName("pop-show")
                for (let i=0;i<popShow.length;i++) {
                    popShow[i].style.display="none"
                }
                switch(type) {
                    //其他
                    case '0':
                        document.getElementsByClassName("pop-show-0")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //钱包服务
                    case '1':
                        document.getElementsByClassName("pop-show-1")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //身份服务
                    case '2':
                        document.getElementsByClassName("pop-show-2")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //转帐服务
                    case '3':
                        document.getElementsByClassName("pop-show-3")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //综合前置机
                    case '4':
                        document.getElementsByClassName("pop-show-4")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //数据库
                    case '5':
                        document.getElementsByClassName("pop-show-5")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = true
                        break
                    //商务网关
                    case '6':
                        document.getElementsByClassName("pop-show-6")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //POS
                    case '7':
                        document.getElementsByClassName("pop-show-7")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //第三方子系统
                    case '8':
                        document.getElementsByClassName("pop-show-8")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //圈存机
                    case '9':
                        document.getElementsByClassName("pop-show-9")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //补卡机
                    case '10':
                        document.getElementsByClassName("pop-show-10")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //充值机
                    case '11':
                        document.getElementsByClassName("pop-show-11")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //一体机
                    case '12':
                        document.getElementsByClassName("pop-show-12")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    default:
				        document.getElementsByClassName("pop-show-0")[0].style.display="block"
	                    _this.popResource = false
	                    _this.popEdition = false
	                    _this.popInfo = false
	                    _this.popSubsystem = false
	                    _this.popOracle = false
	                    break
                }
                this.spinShow = false
                this.drawerShow = true
            })
            .catch(function (error) {
               const str = error + ''
               if (str.search('timeout') !== -1) {
                   _this.$Message.error('请求超时')
                   _this.spinShow = false
               }
             })
        },
        showAnomaliesText () {
        	if (this.textShow === false) {
        		this.spinShow = true
        		this.$ajax.get("errorAlertOperationOverview.do")
	            .then(res => {
	            	this.abnormalList = res.data
	            	this.textShow = true
	            	this.spinShow = false
	            })
        	} else {
        		this.textShow = false
        	}
        },
        showOthers () {
        	this.spinShow = true
        	this.$ajax.get("static/aaa.json")
	            .then(res => {
	            	this.showOthersBtn = false
	                this.others = res.data.data_qita
	                this.spinShow = false
	            })
        },
        hideOthers () {
            this.showOthersBtn = true
            this.others = []
        }
	},
    mounted() {
        this.$ajax.get("static/Overview.json")
        // this.$ajax.get("operationOverview.do")
            .then(res => {
                this.services = res.data.data_fuwu
                this.systems = res.data.data_zixitong
                this.equipments = res.data.data_shebei
                this.spinShow = false
            })
    }
}
</script>



