<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="float: right;margin-left: 8px;">添加</i-button>
            <span>名称：</span>
            <i-input v-model="nameValue" placeholder="请输入名称" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="告警规则设置表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="代码：" prop="RULECODE">
                            <Input v-model="formData.RULECODE" placeholder="请输入代码" :disabled="!addShow" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="名称：" prop="RULENAME">
                            <Input v-model="formData.RULENAME" placeholder="请输入名称" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="联系人：" prop="CONTACTCODE">
                            <Select v-model="selectContact" multiple placeholder="请选择联系人" @on-change="contactChange" clearable>
                                <i-option v-for="item in contactsList" :value="item.CONTACTCODE" :key="contactsL">{{ item.CONTACTNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="抄送人：" prop="COPYTOCODE">
                            <Select v-model="selectCopy" multiple placeholder="请选择抄送人" @on-change="copyChange" clearable>
                                <i-option v-for="item in contactsList" :value="item.CONTACTCODE" :key="contactsL">{{ item.CONTACTNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="短信通知：" prop="RULEBYMOBILE">
                            <RadioGroup v-model="formData.RULEBYMOBILE">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="邮件通知：" prop="RULEBYEMAIL">
                            <RadioGroup v-model="formData.RULEBYEMAIL">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="告警次数：" prop="ALARMCOUNT">
                            <Input v-model="formData.ALARMCOUNT" placeholder="请输入告警次数" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="告警间隔(分钟)：" prop="ALARMINTERVAL">
                            <Input v-model="formData.ALARMINTERVAL" placeholder="请输入告警间隔" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="通知延迟(分钟)：" prop="ALARMDELAY">
                            <Input v-model="formData.ALARMDELAY" placeholder="请输入告警延迟" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="备注：" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-show="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-show="!addShow">提交</Button>
            </div>
        </Drawer>
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateNumber = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        return {
            selectCopy: [],
            selectContact: [],
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            contactsList: [],
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                RULECODE: '',
                RULENAME: '',
                CONTACTCODE: '',
                COPYTOCODE: '',
                RULEBYMOBILE: '',
                RULEBYEMAIL: '',
                ALARMCOUNT: '',
                ALARMINTERVAL: '',
                ALARMDELAY: '',
                MEMO: ''
            },
            ruleValidate: {
                RULECODE: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNumber, trigger: 'blur' }
                ],
                RULENAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                CONTACTCODE: [
                    { required: true, message: '不能为空', trigger: 'change' }
                ],
                ALARMCOUNT: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                ALARMDELAY: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                ALARMINTERVAL: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '代码',
                    key: 'RULECODE'
                },
                {
                    title: '名称',
                    key: 'RULENAME'
                },
                {
                    title: '联系人',
                    key: 'CONTACTNAME'
                },
                {
                    title: '抄送人',
                    key: 'COPYTONAME'
                },
                {
                    title: '短信通知',
                    key: 'RULEBYMOBILE',
                    render (h, params) {
                        return h('div',params.row.RULEBYMOBILE == 1 ? '是' : '否')
                    }
                },
                {
                    title: '邮件通知',
                    key: 'RULEBYEMAIL',
                    render (h, params) {
                        return h('div',params.row.RULEBYEMAIL == 1 ? '是' : '否')
                    }
                },
                {
                    title: '告警次数',
                    key: 'ALARMCOUNT'
                },
                {
                    title: '告警间隔(分钟)',
                    key: 'ALARMINTERVAL'
                },
                {
                    title: '通知延迟(分钟)',
                    key: 'ALARMDELAY'
                },
                {
                    title: '备注',
                    key: 'MEMO'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改'),
                            h('i-button',{
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.removeConfirm(params.index)
                                    }
                                }
                            },'删除')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
            this.formData.RULEBYMOBILE = '是'
            this.formData.RULEBYEMAIL = '是'
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.RULENAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            _formData.RULEBYMOBILE = _showList.RULEBYMOBILE == 1 ? '是' : '否'
            _formData.RULEBYEMAIL = _showList.RULEBYEMAIL == 1 ? '是' : '否'
            this.selectContact = this.formData.CONTACTCODE.split(',')
            this.selectCopy = this.formData.COPYTOCODE.split(',')
            this.drawerShow = true
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("addAlarmRuleNew.do",{
                            params: {
                                RULECODE: this.formData.RULECODE,
                                RULENAME: this.formData.RULENAME,
                                CONTACTCODE: this.formData.CONTACTCODE,
                                COPYTOCODE: this.formData.COPYTOCODE,
                                RULEBYMOBILE: this.formData.RULEBYMOBILE == '是' ? 1 : 0,
                                RULEBYEMAIL: this.formData.RULEBYEMAIL == '是' ? 1 : 0,
                                ALARMCOUNT: this.formData.ALARMCOUNT,
                                ALARMINTERVAL: this.formData.ALARMINTERVAL,
                                ALARMDELAY: this.formData.ALARMDELAY,
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listAlarmRuleNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editAlarmRuleNew.do",{
                            params: {
                                RULECODE: this.formData.RULECODE,
                                RULENAME: this.formData.RULENAME,
                                CONTACTCODE: this.formData.CONTACTCODE,
                                COPYTOCODE: this.formData.COPYTOCODE,
                                RULEBYMOBILE: this.formData.RULEBYMOBILE == '是' ? 1 : 0,
                                RULEBYEMAIL: this.formData.RULEBYEMAIL == '是' ? 1 : 0,
                                ALARMCOUNT: this.formData.ALARMCOUNT,
                                ALARMINTERVAL: this.formData.ALARMINTERVAL,
                                ALARMDELAY: this.formData.ALARMDELAY,
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listAlarmRuleNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.RULECODE = this.showList[index].RULECODE
        },
        remove () {
            this.delShow = false
            this.$ajax.get("removeAlarmRuleNew.do",{
                    params: {
                        RULECODE: this.formData.RULECODE,
                    }
                })
                .then(res => {
                    if (res.data.success == 'true') {
                        this.drawerShow = false
                        this.$Message.success('删除成功')
                        this.spinShow = true
                        this.$ajax.get("listAlarmRuleNew.do")
                            .then(res => {
                                this.totalList = res.data
                                this.searchList = this.totalList
                                this.tableLoad()
                                this.spinShow = false
                            })
                    } else {
                        this.$Message.error(res.data.msg)
                    }
                })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].RULENAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        contactChange () {
            const _this = this
            _this.formData.CONTACTCODE = ''
            for (let i=0;i<this.selectContact.length;i++) {
                _this.formData.CONTACTCODE +=  ',' + this.selectContact[i]
            }
            _this.formData.CONTACTCODE = _this.formData.CONTACTCODE.substr(1)
        },
        copyChange () {
            const _this = this
            _this.formData.COPYTOCODE = ''
            for (let i=0;i<this.selectCopy.length;i++) {
                _this.formData.COPYTOCODE +=  ',' + this.selectCopy[i]
            }
            _this.formData.COPYTOCODE = _this.formData.COPYTOCODE.substr(1)
        }
    },
    mounted() {
        this.$ajax.get("listAlarmRuleNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
        this.$ajax.get("getContactListNew.do")
            .then(res => {
                this.contactsList = res.data
            })
    }
}
</script>



