<template>
    <div>
        <div class="search-con">
            <span>名称：</span>
            <i-input v-model="nameValue" placeholder="请输入名称" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="终端类型表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="代码：" prop="TERMTYPECODE">
                            <Input v-model="formData.TERMTYPECODE"  :disabled="true" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="名称：" prop="TERMTYPENAME">
                            <Input v-model="formData.TERMTYPENAME"  :disabled="true" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="所属子系统：" prop="SYSCODE">
                            <Select v-model="formData.SYSCODE" placeholder="请选择所属子系统" clearable>
                                <i-option v-for="item in sysList" :value="item.SYSCODE" :key="ruleL">{{ item.SYSNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="终端扫描周期(秒)：" prop="TERMINTERVAL">
                            <Input v-model="formData.TERMINTERVAL" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="脱网容忍时间(秒)：" prop="TERMOFFLINETIME">
                            <Input v-model="formData.TERMOFFLINETIME" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="是否为主机设备：" prop="ISHOST">
                            <RadioGroup v-model="formData.ISHOST">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="是否全天监控：" prop="ISMONITORALLDAY">
                            <RadioGroup v-model="formData.ISMONITORALLDAY" @on-change="allDayChange">
                                <Radio value="1" label="是"></Radio>
                                <Radio value="0" label="否"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16" v-show="allDayShow">
                    <Col span="12">
                        <FormItem label="监控时间：" prop="MONITORALLTIME">
                            <Time-picker confirm v-model="formData.MONITORALLTIME" format="HH:mm" type="timerange" placeholder="选择时间" style="width: 168px" @on-change="changeTime"></Time-picker>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="24">
                        <FormItem label="备注：" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="editSubmit('formData')">提交</Button>
            </div>
        </Drawer>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            ruleList: [],
            allDayShow: true,
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                TERMTYPECODE: '',
                TERMTYPENAME: '',
                SYSCODE: '',
                TERMINTERVAL: '',
                TERMOFFLINETIME: '',
                ISHOST: '',
                ISMONITORALLDAY: '',
                MONITORSTARTTIME: '',
                MONITORENDTIME: '',
                MONITORALLTIME: ['',''],
                MEMO: ''
            },
            ruleValidate: {
                TERMINTERVAL: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                TERMOFFLINETIME: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '代码',
                    key: 'TERMTYPECODE'
                },
                {
                    title: '名称',
                    key: 'TERMTYPENAME'
                },
                {
                    title: '所属子系统',
                    key: 'SYSNAME',
                    render: (h, params) => {
                        let text = ''
                        const _this = this
                        for (let i=0;i<_this.sysList.length;i++) {
                            if (_this.sysList[i].SYSCODE == params.row.SYSCODE) {
                                text = _this.sysList[i].SYSNAME
                            }
                        }
                        return h('div',text)
                    }
                },
                {
                    title: '扫描周期(秒)',
                    key: 'TERMINTERVAL'
                },
                {
                    title: '脱网容忍时间(秒)',
                    key: 'TERMOFFLINETIME'
                },
                {
                    title: '是否为主机设备',
                    key: 'ISHOST',
                    render (h, params) {
                        return h('div',params.row.ISVALID == 1 ? '是' : '否')
                    }
                },
                {
                    title: '是否全天监控',
                    key: 'ISMONITORALLDAY',
                    render (h, params) {
                        return h('div',params.row.ISMONITORALLDAY == 1 ? '是' : '否')
                    }
                },
                {
                    title: '监控开始时间',
                    key: 'MONITORSTARTTIME'
                },
                {
                    title: '监控结束时间',
                    key: 'MONITORENDTIME'
                },
                {
                    title: '备注',
                    key: 'MEMO'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 80,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改')
                        ])
                    }
                }
            ],
            sysList: [{"SYSCODE":"0","SYSNAME":"其它"},{"SYSCODE":"1","SYSNAME":"一卡通系统中心"},{"SYSCODE":"2","SYSNAME":"综合前置机"},{"SYSCODE":"3","SYSNAME":"转帐前置机"},{"SYSCODE":"4","SYSNAME":"查询前置机"},{"SYSCODE":"5","SYSNAME":"综合业务子系统"},{"SYSCODE":"6","SYSNAME":"会计子系统"},{"SYSCODE":"7","SYSNAME":"以太网POS子系统"},{"SYSCODE":"8","SYSNAME":"商务子系统"},{"SYSCODE":"9","SYSNAME":"上机子系统"},{"SYSCODE":"10","SYSNAME":"医疗子系统"},{"SYSCODE":"11","SYSNAME":"学籍子系统"},{"SYSCODE":"12","SYSNAME":"图书子系统"},{"SYSCODE":"13","SYSNAME":"门禁子系统"},{"SYSCODE":"14","SYSNAME":"考勤子系统"},{"SYSCODE":"15","SYSNAME":"电控子系统"},{"SYSCODE":"16","SYSNAME":"水控子系统"},{"SYSCODE":"17","SYSNAME":"电话查询子系统"},{"SYSCODE":"18","SYSNAME":"触摸屏子系统"},{"SYSCODE":"19","SYSNAME":"收费子系统"},{"SYSCODE":"20","SYSNAME":"身份前置机"},{"SYSCODE":"21","SYSNAME":"游泳馆子系统"},{"SYSCODE":"22","SYSNAME":"教务子系统"},{"SYSCODE":"23","SYSNAME":"通用场馆收费系统"},{"SYSCODE":"24","SYSNAME":"研究生系统"},{"SYSCODE":"25","SYSNAME":"人事系统"},{"SYSCODE":"26","SYSNAME":"注册系统"},{"SYSCODE":"27","SYSNAME":"选课子系统"},{"SYSCODE":"28","SYSNAME":"物业子系统"},{"SYSCODE":"29","SYSNAME":"考试子系统"},{"SYSCODE":"30","SYSNAME":"会议子系统"},{"SYSCODE":"31","SYSNAME":"巡更子系统"},{"SYSCODE":"32","SYSNAME":"校园BUS系统"},{"SYSCODE":"33","SYSNAME":"第三方对接子系统"},{"SYSCODE":"34","SYSNAME":"车载子系统"},{"SYSCODE":"35","SYSNAME":"第三方电话对接"},{"SYSCODE":"36","SYSNAME":"分布式子系统"},{"SYSCODE":"37","SYSNAME":"会议考勤子系统"},{"SYSCODE":"38","SYSNAME":"自助复印子系统"},{"SYSCODE":"39","SYSNAME":"圈存机子系统"},{"SYSCODE":"40","SYSNAME":"迎新注册离校管理系统"},{"SYSCODE":"41","SYSNAME":"洗衣机子系统"},{"SYSCODE":"42","SYSNAME":"营房管理系统"},{"SYSCODE":"43","SYSNAME":"照片采集子系统"},{"SYSCODE":"44","SYSNAME":"监控子系统"},{"SYSCODE":"45","SYSNAME":"领导查询子系统"},{"SYSCODE":"46","SYSNAME":"电子支付平台"},{"SYSCODE":"47","SYSNAME":"电子支付统一接口"},{"SYSCODE":"48","SYSNAME":"短信平台系统"},{"SYSCODE":"49","SYSNAME":"体育馆综合管理系统"},{"SYSCODE":"50","SYSNAME":"广告管理系统"},{"SYSCODE":"51","SYSNAME":"移动前置机"},{"SYSCODE":"52","SYSNAME":"短信平台系统功能版"},{"SYSCODE":"53","SYSNAME":"浙江移动省平台对接"},{"SYSCODE":"54","SYSNAME":"湖南移动企业一卡通手机支付充值"},{"SYSCODE":"55","SYSNAME":"甘肃省电信平台对接"},{"SYSCODE":"56","SYSNAME":"中控门禁集成"},{"SYSCODE":"57","SYSNAME":"中控考勤集成"},{"SYSCODE":"58","SYSNAME":"一卡通数据同步服务"},{"SYSCODE":"59","SYSNAME":"无线pos"},{"SYSCODE":"60","SYSNAME":"高端手持机代理"},{"SYSCODE":"200","SYSNAME":"一卡通身份后台"}]
        }
    },
    methods: {
        edit (index) {
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.TERMTYPENAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (arr[i] != 'MONITORALLTIME') {
                    if (_showList[arr[i]] == '') {
                        _formData[arr[i]] = _showList[arr[i]].toString()
                    } else if (_showList[arr[i]] == null) {
                        _formData[arr[i]] = ''
                    } else {
                        _formData[arr[i]] = _showList[arr[i]].toString()
                    }
                }
            }
            _formData.MONITORALLTIME[0] = _showList.MONITORSTARTTIME
            _formData.MONITORALLTIME[1] = _showList.MONITORENDTIME
            _formData.ISHOST = _showList.ISHOST == 1 ? '是' : '否'
            _formData.ISMONITORALLDAY = _showList.ISMONITORALLDAY == 1 ? '是' : '否'
            if (_formData.ISMONITORALLDAY == '是') {
                this.allDayShow = false
                _formData.MONITORALLTIME = ['','']
            }
            this.drawerShow = true
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editTermTypeNew.do",{
                            params: {
                                TERMTYPECODE: this.formData.TERMTYPECODE,
                                TERMTYPENAME: this.formData.TERMTYPENAME,
                                SYSCODE: this.formData.SYSCODE,
                                TERMINTERVAL: this.formData.TERMINTERVAL,
                                TERMOFFLINETIME: this.formData.TERMOFFLINETIME,
                                ISHOST: this.formData.ISHOST == '是' ? 1 : 0,
                                ISMONITORALLDAY: this.formData.ISMONITORALLDAY == '是' ? 1 : 0,
                                MONITORSTARTTIME: this.formData.MONITORALLTIME[0],
                                MONITORENDTIME: this.formData.MONITORALLTIME[1],
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listTermTypeNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMTYPENAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        allDayChange () {
            this.formData.ISMONITORALLDAY == '是' ? this.allDayShow = false : this.allDayShow = true
            this.formData.MONITORALLTIME = ['','']
        }
    },
    mounted() {
        this.$ajax.get("listTermTypeNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



