<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="margin-left: 8px;float: right;">添加</i-button>
            <span>帐号：</span>
            <i-input v-model="userValue" placeholder="请输入帐号" style="width: 150px;margin-right: 8px;"></i-input>
            <span>姓名：</span>
            <i-input v-model="nameValue" placeholder="请输入姓名" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="用户管理表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="帐号：" prop="USERID">
                            <Input v-model="formData.USERID" placeholder="请输入帐号" :disabled="!addShow" />
                        </FormItem>
                    </Col>
                     <Col span="12" v-if="addShow">
                        <FormItem label="密码：" prop="USERPWD">
                            <Input type="password" v-model="formData.USERPWD" placeholder="请输入密码" />
                        </FormItem>
                    </Col>
                    <Col span="12" v-if="!addShow">
                        <FormItem label="密码：" prop="USERPWD">
                            <Input type="password" v-model="formData.USERPWD" @on-focus="changePassword" @on-blur="passwordBlur" placeholder="请输入密码" />
                        </FormItem>
                        <Input type="hidden" v-model="oldPassword" placeholder="请输入密码" />
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="姓名：" prop="REALNAME">
                            <Input v-model="formData.REALNAME" placeholder="请输入姓名" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="电话：" prop="PHONENUM">
                            <Input v-model="formData.PHONENUM" placeholder="请输入电话号" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="手机：" prop="MOBILENUM">
                            <Input v-model="formData.MOBILENUM" placeholder="请输入手机号" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="邮箱：" prop="EMAIL">
                            <Input v-model="formData.EMAIL" placeholder="请输入邮箱" />
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-show="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-show="!addShow">提交</Button>
            </div>
        </Drawer> 
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        const validateTel = (rule, value, callback) => {
            const reg = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的电话号'))
            } else {
                callback()
            }
        }
        const validatePhone = (rule, value, callback) => {
            const reg = /^1[34578]\d{9}$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的手机号'))
            } else {
                callback()
            }
        }
        return {
            oldPassword: '',
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                USERID: '',
                USERPWD: '',
                REALNAME: '',
                PHONENUM: '',
                MOBILENUM: '',
                EMAIL: ''
            },
            ruleValidate: {
                USERID: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                USERPWD: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                REALNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                PHONENUM: [
                    { validator: validateTel, trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ],
                MOBILENUM: [
                    { validator: validatePhone, trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ],
                EMAIL: [
                    { type: 'email', message: '请输入正确邮箱', trigger: 'blur' },
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            userValue: '',
            columns: [
                {
                    title: '帐号',
                    key: 'USERID'
                },
                {
                    title: '姓名',
                    key: 'REALNAME'
                },
                {
                    title: '电话',
                    key: 'PHONENUM'
                },
                {
                    title: '手机',
                    key: 'MOBILENUM'
                },
                {
                    title: '邮箱',
                    key: 'EMAIL'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        if (self.showList[params.index].USERID == 'admin') {
                            return h('div',[
                                h('i-button',{
                                    props: {
                                        type: 'warning',
                                        size: 'small'
                                    },
                                    attrs: {
                                        style: "margin-right: 10px;"
                                    },
                                    on: {
                                        click: () => {
                                            self.edit(params.index)
                                        }
                                    }
                                },'修改')
                            ])
                        } else {
                            return h('div',[
                                h('i-button',{
                                    props: {
                                        type: 'warning',
                                        size: 'small'
                                    },
                                    attrs: {
                                        style: "margin-right: 10px;"
                                    },
                                    on: {
                                        click: () => {
                                            self.edit(params.index)
                                        }
                                    }
                                },'修改'),
                                h('i-button',{
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            self.removeConfirm(params.index)
                                        }
                                    }
                                },'删除')
                            ])
                        }
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.USERID
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            _formData.USERPWD = '9782635410'
            this.oldPassword = _showList.USERPWD
            this.drawerShow = true
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("addUserNew.do",{
                            params: {
                                USERID: this.formData.USERID,
                                USERPWD: this.formData.USERPWD,
                                REALNAME: this.formData.REALNAME,
                                PHONENUM: this.formData.PHONENUM,
                                MOBILENUM: this.formData.MOBILENUM,
                                EMAIL: this.formData.EMAIL
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listUserNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            let paramsData = {}
            this.$refs[name].validate((valid) => {
                if (valid) {
                    if (this.formData.USERPWD == '9782635410') {
                        paramsData = {
                            USERID: this.formData.USERID,
                            USERPWD: '',
                            USERPWD1: this.oldPassword,
                            REALNAME: this.formData.REALNAME,
                            PHONENUM: this.formData.PHONENUM,
                            MOBILENUM: this.formData.MOBILENUM,
                            EMAIL: this.formData.EMAIL
                        }
                    } else {
                        paramsData = {
                            USERID: this.formData.USERID,
                            USERPWD: this.formData.USERPWD,
                            USERPWD1: '',
                            REALNAME: this.formData.REALNAME,
                            PHONENUM: this.formData.PHONENUM,
                            MOBILENUM: this.formData.MOBILENUM,
                            EMAIL: this.formData.EMAIL
                        }
                    }
                    this.$ajax.get("editUserNew.do",{
                            params: paramsData
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listUserNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.USERID = this.showList[index].USERID
        },
        remove () {
            this.delShow = false
            this.$ajax.get("removeUserNew.do",{
                    params: {
                        USERID: this.formData.USERID
                    }
                })
                .then(res => {
                    if (res.data.success == 'true') {
                        this.$Message.success('删除成功')
                        this.spinShow = true
                        this.$ajax.get("listUserNew.do")
                            .then(res => {
                                this.totalList = res.data
                                this.searchList = this.totalList
                                this.tableLoad()
                                this.spinShow = false
                            })
                    } else {
                        this.$Message.error(res.data.msg)
                    }
                })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            if (this.userValue != '') {
                this.searchUser()
            }
            this.tableLoad()
        },
        searchUser () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].USERID.indexOf(this.userValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].REALNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        changePassword () {
            if (this.formData.USERPWD == '9782635410') {
               this.formData.USERPWD = ''
            }
        },
        passwordBlur () {
            if (this.formData.USERPWD == '') {
               this.formData.USERPWD = '9782635410'
            }
        }
    },
    mounted() {
        this.$ajax.get("listUserNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



