<template>
	<div style="position: relative;">
		<div class="abnormal-text-list">
			<Icon type="md-list" size="38" style="position: absolute;top: 8px;right: 8px;cursor: pointer;color: #fff;" @click="showAnomaliesText"></Icon>
			<ul v-show="textShow">
				<li v-for="item in abnormalList"><span>[{{ item.TYPE }}]</span><span> {{ item.TERMNAME }}：</span>{{ item.EXCEPTIONDESC }}<span>　{{ item.EXCEPTIONSTART }}</span></li>
			</ul>
		</div>
		<h3 class="blue-title">服务异常情况 ({{ services.length }})：</h3>
		<p v-show="!serviceShow" class="mb-24">　无异常服务</p>
		<ul class="overview-list overview-service" v-show="serviceShow">
			<li v-for="(service,index) in services" :class="service.RUNSTATUS == 1 ? 'on' : service.RUNSTATUS == 0 ? 'off' : 'error'" @click="show(index,services)">{{ service.TERMNAME }}</li>
		</ul>
		<h3 class="blue-title">子系统异常情况 ({{ systems.length }})：</h3>
		<p v-show="!systemShow" class="mb-24">　无异常子系统</p>
		<ul class="overview-list overview-system" v-show="systemShow">
			<li v-for="(system,index) in systems" :class="(system.RUNSTATUS == 1 ? 'on' : system.RUNSTATUS == 0 ? 'off' : 'error') + ' ' + (system.ALERTTYPECODE == 6 ? 'business' : 'normal')" @click="show(index,systems)">{{ system.TERMNAME }}</li>
		</ul>
		<h3 class="blue-title">自助设备异常情况 ({{ equipments.length }})：</h3>
		<p v-show="!equipmentShow" class="mb-24">　无异常设备</p>
		<ul class="overview-list overview-equipment" v-show="equipmentShow">
			<li v-for="(equipment,index) in equipments" :class="(equipment.RUNSTATUS == 1 ? 'on' : equipment.RUNSTATUS == 0 ? 'off' : 'error') + ' ' + (equipment.ALERTTYPECODE == 9 ? 'deposit' : equipment.ALERTTYPECODE == 10 ? 'card' : equipment.ALERTTYPECODE == 11 ? 'money' : 'normal')" @click="show(index,equipments)">{{ equipment.TERMNAME }}</li>
		</ul>
		<h3 class="blue-title">其他设备异常情况 ({{ others.length }})：</h3>
		<p v-show="!otherShow" class="mb-24">　无异常设备</p>
		<ul class="overview-list overview-equipment" v-show="otherShow">
			<li v-for="(other,index) in others" :class="(other.RUNSTATUS == 1 ? 'on' : other.RUNSTATUS == 0 ? 'off' : 'error') + ' ' + 'other'" @click="show(index,others)">{{ other.PARENTTERMNAME }}{{ other.TERMNAME }}</li>
		</ul>
		<Spin fix v-if="spinShow">
	        <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
	        <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
	    </Spin>
	</div>
</template>
<script>
export default {
	data () {
		return {
			abnormalList: [],
			services: [],
			systems: [],
			equipments: [],
			others: [],
			serviceShow: true,
			systemShow: true,
			equipmentShow: true,
			otherShow: true,
			textShow: false,
			spinShow: true
		}
	},
	methods: {
		show (index,n) {
            this.$Modal.info({
                title: `${n[index].TERMNAME}`,
                content: `监控编号：${n[index].TERMNO}<br>IP地址：${n[index].TERMIP == undefined ? '' : n[index].TERMIP}<br>服务端口：${n[index].TERMPORT == undefined ? '' : n[index].TERMPORT}<br>异常描述：${n[index].EXCEPTIONDESC}<br>发生时间：${n[index].EXCEPTIONSTART}<br>扫描时间：${n[index].ROLLCALLTIME}`
            })
        },
        showAnomaliesText () {
        	if (this.textShow === false) {
        		this.spinShow = true
        		this.$ajax.get("errorAlertOperationOverview.do")
	            .then(res => {
	            	this.abnormalList = res.data
	            	this.textShow = true
	            	this.spinShow = false
	            })
        	} else {
        		this.textShow = false
        	}
        }
	},
    mounted() {
        this.$ajax.get("static/Abnormal.json")
            .then(res => {
            	this.services = res.data.data_fuwu
            	this.systems = res.data.data_zixitong
            	this.equipments = res.data.data_shebei
            	this.others = res.data.data_qita
            	this.services.length == 0 ? this.serviceShow = false : this.serviceShow = true
            	this.systems.length == 0 ? this.systemShow = false : this.systemShow = true
            	this.equipments.length == 0 ? this.equipmentShow = false : this.equipmentShow = true
            	this.others.length == 0 ? this.otherShow = false : this.otherShow = true
            	this.spinShow = false
            })
    }
}
</script>



