<style scoped>
table {
	font-size: 14px;
	margin-bottom: 24px;
}
td {
	width: 200px;
	padding: 8px 8px;
	border: 1px solid #515a6e;
}
tr td:nth-child(odd) {
	width: 150px;
	text-align: right;
}
.little-table td {
	padding: 4px 8px;
}
.little-table td:nth-child(odd) {
	width: 52px;
	text-align: center;
}
.little-table tr td:nth-child(even) {
	width: 86px;
	text-align: center;
}
</style>
<template>
	<div>
		<h3 style="margin-bottom: 24px;color: #2980b9;">后台运行情况：</h3>
		<table>
			<tr>
				<td>扫 描 时 间：</td>
				<td>{{ PurseInfo.ROLLCALLTIME }}</td>
				<td>工作状态：</td>
				<td>{{ PurseInfo.WORKINGSTATE }}</td>
			</tr>
			<tr>
				<td>持卡总人数：</td>
				<td>{{ PurseInfo.ACCOUNTNO }}</td>
				<td>双机热备：</td>
				<td>{{ PurseInfo.ISWARMBACKUPOK == 1 ? '正常' : PurseInfo.ISWARMBACKUPOK == 0 ? '异常' : '' }}</td>
			</tr>
			<tr>
				<td>当前业务量：</td>
				<td>{{ PurseInfo.BUSSTOTAL }}</td>
				<td>日结情况：</td>
				<td>{{ PurseInfo.KEEPSTATUS == 1 ? '成功' : PurseInfo.KEEPSTATUS == 0 ? '失败' : '' }}</td>
			</tr>
			<tr>
				<td>数据库备份时间：</td>
				<td>{{ PurseInfo.DBBACKUPTIME }}</td>
				<td>数据库备份情况：</td>
				<td>{{ PurseInfo.ISDBBACKUP == 1 ? '成功' : PurseInfo.ISDBBACKUP == 0 ? '失败' : '' }}</td>
			</tr>
		</table>
		<h3 style="margin-bottom: 24px;color: #2980b9;">服务运行情况：</h3>
		<table class="little-table">
			<tr>
				<td>正常</td>
				<td>{{ OverallSer.NORMAL }}</td>
				<td>异常</td>
				<td>{{ OverallSer.EXCEPTION }}</td>
				<td>未知</td>
				<td>{{ OverallSer.OFFSTATUS }}</td>
				<td>签退</td>
				<td>{{ OverallSer.SIGNOUT }}</td>
				<td>总数</td>
				<td>{{ OverallSer.NORMAL + OverallSer.EXCEPTION +  OverallSer.OFFSTATUS + OverallSer.SIGNOUT }}</td>
			</tr>
		</table>
		<h3 style="margin-bottom: 24px;color: #2980b9;">子系统运行情况：</h3>
		<table class="little-table">
			<tr>
				<td>正常</td>
				<td>{{ OverallSys.NORMAL }}</td>
				<td>异常</td>
				<td>{{ OverallSys.EXCEPTION }}</td>
				<td>未知</td>
				<td>{{ OverallSys.OFFSTATUS }}</td>
				<td>签退</td>
				<td>{{ OverallSys.SIGNOUT }}</td>
				<td>总数</td>
				<td>{{ OverallSys.NORMAL + OverallSys.EXCEPTION + OverallSys.OFFSTATUS + OverallSys.SIGNOUT }}</td>
			</tr>
		</table>
		<h3 style="margin-bottom: 24px;color: #2980b9;">设备运行情况：</h3>
		<table class="little-table">
			<tr>
				<td>正常</td>
				<td>{{ OverallTerm.NORMAL }}</td>
				<td>异常</td>
				<td>{{ OverallTerm.EXCEPTION }}</td>
				<td>未知</td>
				<td>{{ OverallTerm.OFFSTATUS }}</td>
				<td>签退</td>
				<td>{{ OverallTerm.SIGNOUT }}</td>
				<td>总数</td>
				<td>{{ OverallTerm.NORMAL + OverallTerm.EXCEPTION +  OverallTerm.OFFSTATUS + OverallTerm.SIGNOUT }}</td>
			</tr>
		</table>
		<Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
	</div>
</template>
<script>
export default {
	data () {
		return {
			spinShow: true,
			OverallSer: {},
			OverallSys: {},
			OverallTerm: {},
			PurseInfo: {}
		}
	},
	mounted () {
		this.$ajax.get("static/Overall.json")
            .then(res => {
            	this.PurseInfo = res.data.PurseInfo
                this.OverallSer = res.data.OverallSer
                this.OverallSys = res.data.OverallSys
                this.OverallTerm = res.data.OverallTerm
                this.spinShow = false
            })
	}
}
</script>



