<template>
    <div>
        <div v-if="searchShow" style="position: absolute;top: 15px;right: 25px;margin-bottom: 12px;z-index: 2;">
            <span>日期：</span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="请选择日期" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs @on-click="clickTab">
            <TabPane label="当天消费可疑流水" icon="ios-sunny">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px;"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
            <TabPane label="历史消费可疑流水" icon="md-time" style="position: relative;min-height: 300px;">
                <i-table border stripe :columns="columns" :data="showList2" style="min-width: 800px;"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal2" :current="pageCurrent2" :page-size="pageSize" @on-change="changePage2" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            spinShow: true,
            searchShow: '',
            dateValue: '',
            totalList: [],
            showList: [],
            searchList: [],
            totalList2: [],
            showList2: [],
            dataTotal2: 0,
            searchList2: [],
            pageCurrent2: 1,
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            operatorList: [],
            selectOperator: '',
            columns: [
                {
                    title: '子系统名称',
                    key: 'SYSNAME'
                },
                {
                    title: '监控编号',
                    key: 'POSCODE'
                },
                {
                    title: '发端流水号',
                    key: 'FROMJNNUMBER'
                },
                {
                    title: '帐户名称',
                    key: 'ACCOUNTNAME'
                },
                {
                    title: '帐号',
                    key: 'FROMACCOUNT'
                },
                {
                    title: '卡类型',
                    key: 'CARDTYPENAME'
                },
                {
                    title: '交易时间',
                    key: 'JNDATETIME'
                },
                {
                    title: '消费金额(元)',
                    key: 'TRANAMT',
                    render: (h, params) => {
                        let value = params.row.TRANAMT + ''
                        value = value.replace(/-/g, "")
                        if (value == 'null' || value == 0 || value == '0') {
                            value = "0.00"
                        } else {
                            let v = "" + value;
                            if (v.indexOf(".") != -1) {
                                value = v
                            } else {
                                for (let i=v.length;i<3;i++) {
                                    v = 0 + v
                                }
                                value = v.substring(0, v.length - 2) + "." + v.substring(v.length - 2);
                            }
                        }
                        return h('div',value)
                    }
                },
                {
                    title: '可疑原因',
                    key: 'SUSPECTNAME'
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now() - 86400000;
                }
            }
        }
    },
    methods: {
        clickTab (n) {
            if (n == 1) {
                this.searchShow = true
                const today = new Date()
                const lastDay = new Date(today.getTime() - 1 * 24 * 3600 * 1000)
                const lastSevenDay = new Date(today.getTime() - 7 * 24 * 3600 * 1000)
                this.dateValue = [lastSevenDay,lastDay]
                const paramsData = {
                    startdate: this.padDate(this.dateValue[0]).replace(/-/g, ""),
                    enddate: this.padDate(this.dateValue[1]).replace(/-/g, ""),
                    trancode : 15
                }
                this.ajaxData(paramsData)
            } else {
                this.searchShow = false
            }
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        changePage2 (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList2 = this.searchList2.slice(_start, _end)
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        ajaxData (paramsData) {
            const selfError = this
            this.spinShow = true
            this.$ajax.get("getHisSuspectFlowNew.do",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList2 = res.data
                    this.searchList2 = this.totalList2
                    this.tableLoad2()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        },
        tableSearch () {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]).replace(/-/g, ""),
                enddate: this.padDate(this.dateValue[1]).replace(/-/g, ""),
                trancode : 15
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        tableLoad2 () {
            this.dataTotal2 = this.searchList2.length
            if (this.searchList2 < this.pageSize) {
                this.showList2 = this.searchList2
            } else {
                this.showList2 = this.searchList2.slice(0,this.pageSize)
            }
            this.pageCurrent2 = 1
        }
    },
    mounted() {
        const today = new Date()
        this.dateValue = [today,today]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1]),
            trancode : 15
        }
        this.$ajax.get("getSuspectFlowNew.do",{
                params: paramsData
            })
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



