<template>
    <div>
        <div class="search-con">
            <span>年份：</span>
            <date-picker v-model="dateValue" type="year" format="yyyy" placeholder="请选择年份" style="width: 200px;margin-right: 8px;" :editable="false"></date-picker>
            <!-- <span>终端类型：</span>
            <i-select v-model="selectTerminal" style="width: 200px;margin-right: 8px;" placeholder="请选择终端类型">
                <i-option v-for="item in terminalList" :value="item.termtypecode" :key="terminalL">{{ item.termtypename }}</i-option>
            </i-select> -->
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs @on-click="clickTab">
            <TabPane label="年度异常统计表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px" :style="{marginTop :marginTop}"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
            <TabPane label="年度异常分析图" icon="md-pie" style="position: relative;min-height: 300px;">
                <div :style="{height:height,width:width}" ref="myEchart"></div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
import echarts from 'echarts'
export default {
    props: {
        width: {
            type: String,
            default: '100%'
        },
        height: {
            type: String,
            default: '380px'
        }
    },
    data() {
        return {
            spinShow: true,
            selectTerminal: [],
            terminalList: [],
            chart: null,
            aNum: [],
            dateValue: '',
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 12,
            clickTabVal: '',
            columns: [
                {
                    title: '月份',
                    key: 'month'
                },
                {
                    title: '异常次数',
                    key: 'count'
                }
            ]
        }
    },
    mounted() {
        this.dateValue= new Date().getFullYear()
        const paramsData = {
            year: this.dateValue
        }
        this.ajaxData(paramsData)
    },
    methods: {
        clickTab (val) {
            this.clickTabVal = val
            val == 1 ? this.initChart() : this.tableLoad()
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            const paramsData = {
                year: this.dateValue.getFullYear()
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        ajaxData (paramsData) {
            const selfError = this;
            this.spinShow = true
            this.$ajax.get("static/AbnormalCountYear.json",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data.data.year
                    this.terminalList = res.data.nameandcode.data
                    this.searchList = this.totalList
                    const _totalList = this.totalList
                    this.aNum = []
                    if (this.totalList != false) {
                        for (let i=0;i<_totalList.length;i++) {
                            this.aNum[i] = _totalList[i].count
                        }
                    }
                    this.clickTabVal == 1 ? this.initChart() : this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        },
        initChart () {
            this.chart = echarts.init(this.$refs.myEchart)
            this.chart.setOption({
                xAxis: {
                    name: '月份',
                    type: 'category',
                    boundaryGap: false,
                    data: ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月'],
                },
                tooltip : {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: '#6a7985'
                        }
                    }
                },
                yAxis: {
                    name: '发生次数',
                    type: 'value'
                },
                series: [{
                    name:'发生次数',
                    data: this.aNum,
                    type: 'line',
                    areaStyle: {
                        color: '#34495e'
                    }
                }]
            })
        }
    }
}
</script>


