<template>
    <div>
        <div class="search-con">
            <span>日期：</span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="请选择日期" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs @on-click="clickTab">
            <TabPane label="终端异常统计表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px" :style="{marginTop :marginTop}"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
            <TabPane label="终端异常分析图" icon="md-pie" style="position: relative;min-height: 300px;">
                <div :style="{height:height,width:width}" ref="myEchart"></div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
import echarts from 'echarts'
export default {
    props: {
        width: {
            type: String,
            default: '100%'
        },
        height: {
            type: String,
            default: '380px'
        }
    },
    data() {
        return {
            spinShow: true,
            chart: null,
            tNum: [],
            tName: [],
            eNum: [],
            dateValue: [],
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            clickTabVal: 0,
            columns: [
                {
                    title: '终端类型',
                    key: 'termtypename'
                },
                {
                    title: '终端数量',
                    key: 'termnum'
                },
                {
                    title: '异常次数',
                    key: 'erronum'
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now() - 86400000;
                }
            }
        }
    },
    mounted() {
        let today = new Date()
        const lastDay = new Date(today.getTime() - 1 * 24 * 3600 * 1000)
        let lastSevenDay = new Date(today.getTime() - 6 * 24 * 3600 * 1000)
        this.dateValue = [lastSevenDay,lastDay]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1])
        }
        this.ajaxData(paramsData)
    },
    methods: {
        clickTab (val) {
            this.clickTabVal = val
            val == 1 ? this.initChart() : this.tableLoad()
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]),
                enddate: this.padDate(this.dateValue[1])
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        ajaxData (paramsData) {
            const selfError = this;
            this.spinShow = true
            this.$ajax.get("abnormalRecord.do",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data.data
                    this.searchList = this.totalList
                    const _totalList = this.totalList
                    this.tNum = []
                    this.tName = []
                    this.eNum = []
                    if (this.totalList != false) {
                        for (let i=0;i<_totalList.length;i++) {
                            this.tNum[i] = _totalList[i].termnum
                            this.tName[i] = _totalList[i].termtypename
                            this.eNum[i] = _totalList[i].erronum
                        }
                    }
                    this.clickTabVal == 1 ? this.initChart() : this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        },
        initChart () {
            this.chart = echarts.init(this.$refs.myEchart)
            this.chart.setOption({
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        crossStyle: {
                            color: '#999'
                        }
                    }
                },
                legend: {
                    data:['异常（次）', '终端（台）'],
                    textStyle: {
                        fontSize: 14
                    }
                },
                dataZoom: {
                    show: false,
                    start: 0,
                    end: 100
                },
                xAxis: [
                    {
                        type: 'category',
                        data: this.tName,
                        axisPointer: {
                            type: 'shadow'
                        },
                        textStyle: {
                            fontSize: 14
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        name: '异常（次）',
                        scale: true,
                        boundaryGap: ['0', '15%'],
                        nameTextStyle:{
                            fontSize:'14'
                        }
                    },
                    {
                        type: 'value',
                        name: '终端（台）',
                        min: 0,
                        splitLine: {
                            show: false
                        },
                        nameTextStyle:{
                            fontSize:'14'
                        }
                    }
                ],
                series: [
                    {
                        name:'异常（次）',
                        type:'line',
                        data:this.eNum,
                        lineStyle: {
                            shadowColor : 'rgba(0,0,0,1)',
                            shadowBlur: 1,
                            shadowOffsetX: 1,
                            shadowOffsetY: 1
                        },
                        color: '#e74c3c'
                    },
                    {
                        name:'终端（台）',
                        type:'bar',
                        data:this.tNum,
                        yAxisIndex: 1,
                        color: '#34495e'
                    }
                ]
            })
        }
    }
}
</script>


