import 'babel-polyfill'
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'
import router from './router'
import iView from 'iview'
import axios from 'axios'
import VueCookies from 'vue-cookies'
import 'iview/dist/styles/iview.css'
import './css/main.css'
import BaiduMap from 'vue-baidu-map'

Vue.use(BaiduMap, {
	ak: 'ddf9Nb8e4iMlU4bpqe76768LPjBcDSec'
})

Vue.use(VueCookies)
VueCookies.config('7d')
VueCookies.set('theme','default')
VueCookies.set('hover-time','1s')

Vue.use(VueRouter)
Vue.use(iView)

Vue.prototype.$ajax = axios
axios.defaults.timeout = 30000

Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
	//所有需要检测登录状态的路由页面都放在这个数组里面
	const nextRoute = ['overview','abnormal','overall','server','system','equipment','smap','operation-log','identity-log','terminal-abnormal','terminal-warning','suspicious-pos','suspicious-qcj','user-count','abnormal-count-termina','abnormal-count-type','abnormal-count-year','operation-daily-count','suspicious-count-type','qcj-count-termina','contacts','unit','campus','area','warning-rule','abnormal-rule','suspicious-rule','terminal-type','terminal','data-base','user']
	let isLogin = $cookies.get('status') == 'logined' ? true : false

	if(isLogin) {
		isLogin = true
	}

    if (nextRoute.indexOf(to.path.split('/')[1]) >= 0) {
        if (!isLogin) {
			next({
				path: '/login'
			})
			alert('未登录')
			location.reload()
        }
    }
   
    if (to.path.split('/')[to.path.split('/').length-1] === 'login') {
        if (isLogin) {
			router.push({path:'/overview'})
			location.reload();
        }
    }
    next();
});

new Vue({
	el: '#app',
	router: router,
	render: h => h(App)
});



