<template>
    <div>
        <div class="search-con">
            <span>终端类型：</span>
            <i-input v-model="nameValue" placeholder="请输入终端类型" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="终端类型表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="终端类型：" prop="TERMTYPENAME">
                            <Input v-model="formData.TERMTYPENAME" :disabled="true" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="异常描述：" prop="EXCDESC">
                            <Input v-model="formData.EXCDESC" :disabled="true" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="状态：" prop="ISVALID">
                            <RadioGroup v-model="formData.ISVALID">
                                <Radio value="1" label="启用"></Radio>
                                <Radio value="0" label="停用"></Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="持续时间(分钟)：" prop="EXCTRAPLASTTIME">
                            <Input v-model="formData.EXCTRAPLASTTIME" placeholder="请输入名称" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="告警延迟(分钟)：" prop="EXCTRAPVALUE">
                            <Input v-model="formData.EXCTRAPVALUE" placeholder="请输入报警次数" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="告警规则：" prop="RULECODE">
                            <Select v-model="formData.RULECODE" placeholder="请选择告警规则" clearable>
                                <i-option v-for="item in ruleList" :value="item.RULECODE" :key="ruleL">{{ item.RULENAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="备注：" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="editSubmit('formData')">提交</Button>
            </div>
        </Drawer>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            ruleList: [],
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                EXCCODE: '',
                TERMTYPENAME: '',
                EXCDESC: '',
                ISVALID: '',
                EXCTRAPLASTTIME: '',
                EXCTRAPVALUE: '',
                RULECODE: '',
                MEMO: ''
            },
            ruleValidate: {
                TERMTYPENAME: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                ISVALID: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                EXCTRAPLASTTIME: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                EXCTRAPVALUE: [
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '终端类型',
                    key: 'TERMTYPENAME'
                },
                {
                    title: '异常描述',
                    key: 'EXCDESC'
                },
                {
                    title: '状态',
                    key: 'ISVALID',
                    render (h, params) {
                        return h('div',params.row.ISVALID == 1 ? '启用' : '停用')
                    }
                },
                {
                    title: '持续时间(分钟)',
                    key: 'EXCTRAPLASTTIME'
                },
                {
                    title: '告警延迟(分钟)',
                    key: 'EXCTRAPVALUE'
                },
                {
                    title: '告警规则',
                    key: 'RULENAME'
                },
                {
                    title: '备注',
                    key: 'MEMO'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 80,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        edit (index) {
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改'
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            _formData.ISVALID = _showList.ISVALID == 1 ? '启用' : '停用'
            this.drawerShow = true
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editExceptionDefNew.do",{
                            params: {
                                EXCCODE: this.formData.EXCCODE,
                                TERMTYPENAME: this.formData.TERMTYPENAME,
                                EXCDESC: this.formData.EXCDESC,
                                ISVALID: this.formData.ISVALID == '启用' ? 1 : 0,
                                EXCTRAPLASTTIME: this.formData.EXCTRAPLASTTIME,
                                EXCTRAPVALUE: this.formData.EXCTRAPVALUE,
                                RULECODE: this.formData.RULECODE,
                                MEMO: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listExceptionDefNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMTYPENAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        this.$ajax.get("listExceptionDefNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
        this.$ajax.get("getAlarmRulelistNew.do")
            .then(res => {
                this.ruleList = res.data
            })
    }
}
</script>



