                                <label>已打卡片：</label><input :value="popDetail.detail.CARDSPRINTED" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>剩余卡片：</label><input :value="popDetail.detail.RESERVECARDS" readonly />
                            </Col>
                            <Col span="8">
                                <label>已用色带：</label><input :value="popDetail.detail.PANELSPRINTED" readonly />
                            </Col>
                            <Col span="8">
                                <label>剩余色带：</label><input :value="popDetail.detail.PANELSREMAINING " readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>状态更新时间：</label><input :value="popDetail.detail.EXCEPTIONSTART" readonly />
                            </Col>
                            <Col span="8">
                                <label>扫描时间：</label><input :value="popDetail.detail.ROLLCALLTIME" readonly />
                            </Col>
                        </Row>
                    </div>
                    <div class="pop-show pop-show-11">
                        <Row :gutter="16">
                            <Col span="8">
                                <label>当前状态：</label><input :value="popDetail.detail.RUNSTATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label>状态说明：</label><input :value="popDetail.detail.NEWROLLCALLMEMO" readonly />
                            </Col>
                            <Col span="8">
                                <label>钱箱容量：</label><input :value="popDetail.detail.ACCEPTMAXNUMBER" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>钱箱金额：</label><input :value="popDetail.detail.SUMMONEY" readonly />
                            </Col>
                            <Col span="8">
                                <label>报警张数：</label><input :value="popDetail.detail.HINTNUMBER" readonly />
                            </Col>
                            <Col span="8">
                                <label>纸币张数：</label><input :value="popDetail.detail.CACHNUMBER" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>状态更新时间：</label><input :value="popDetail.detail.EXCEPTIONSTART" readonly />
                            </Col>
                            <Col span="8">
                                <label>扫描时间：</label><input :value="popDetail.detail.ROLLCALLTIME" readonly />
                            </Col>
                        </Row>
                    </div>
                    <div class="pop-show pop-show-12">
                        <Row :gutter="16">
                            <Col span="8">
                                <label>当前状态：</label><input :value="popDetail.detail.RUNSTATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label>状态说明：</label><input :value="popDetail.detail.NEWROLLCALLMEMO" readonly />
                            </Col>
                            <Col span="8">
                                <label>缴费笔数：</label><input :value="popDetail.detail.FEECOUNT" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>缴费金额：</label><input :value="popDetail.detail.FEEMONEY" readonly />
                            </Col>
                            <Col span="8">
                                <label>转账笔数：</label><input :value="popDetail.detail.TRANCOUNT" readonly />
                            </Col>
                            <Col span="8">
                                <label>转账金额：</label><input :value="popDetail.detail.TRANMONEY" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>已打卡片：</label><input :value="popDetail.detail.CARDSPRINTED" readonly />
                            </Col>
                            <Col span="8">
                                <label>剩余卡片：</label><input :value="popDetail.detail.RESERVECARDS" readonly />
                            </Col>
                            <Col span="8">
                                <label>钱箱容量：</label><input :value="popDetail.detail.ACCEPTMAXNUMBER" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>钱箱金额：</label><input :value="popDetail.detail.SUMMONEY" readonly />
                            </Col>
                            <Col span="8">
                                <label>已用色带：</label><input :value="popDetail.detail.PANELSPRINTED" readonly />
                            </Col>
                            <Col span="8">
                                <label>剩余色带：</label><input :value="popDetail.detail.PANELSREMAINING" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>工作状态：</label><input :value="popDetail.detail.STATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label>打印机状态：</label><input :value="popDetail.detail.PRINTERSTATUS" readonly />
                            </Col>
                            <Col span="8">
                                <label>上次交易时间：</label><input :value="popDetail.detail.LASTTRANTIME" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>报警张数：</label><input :value="popDetail.detail.HINTNUMBER" readonly />
                            </Col>
                            <Col span="8">
                                <label>纸币张数：</label><input :value="popDetail.detail.CACHNUMBER" readonly />
                            </Col>
                            <Col span="8">
                                <label>状态更新时间：</label><input :value="popDetail.detail.EXCEPTIONSTART" readonly />
                            </Col>
                        </Row>
                        <Row :gutter="16">
                            <Col span="8">
                                <label>扫描时间：</label><input :value="popDetail.detail.ROLLCALLTIME" readonly />
                            </Col>
                        </Row>
                    </div>
                </TabPane>
                <TabPane label="服务资源占比" icon="md-stats" class=" pop-big" v-if="popResource" name="key1">
                    <i-table border stripe :columns="columnsResource" :data="popDetail.resource"></i-table>
                </TabPane>
                <TabPane label="状态和版本" icon="md-filing" class="pop-big" v-if="popEdition" name="key2">
                    <i-table border stripe :columns="columnsEdition" :data="popDetail.edition"></i-table>
                </TabPane>
                <TabPane label="消息队列情况" icon="md-list" class="pop-big" v-if="popInfo" name="key3">
                    <i-table border stripe :columns="columnsInfo" :data="popDetail.info"></i-table>
                </TabPane>
                <TabPane label="子系统连接情况" icon="md-list" class="pop-big" v-if="popSubsystem" name="key4">
                    <i-table border stripe :columns="columnsSubsystem" :data="popDetail.subsystem"></i-table>
                </TabPane>
                <TabPane label="Oracle数据库监控信息" icon="md-list" class="pop-detail pop-oracle pop-big" v-if="popOracle" name="key5">
                    <Row>
                        <Col span="8">
                            <label>扫描时间：</label><input :value="popDetail.oracle.ROLLCALLTIME" readonly />
                        </Col>
                        <Col span="8">
                            <label>最大连接数：</label><input :value="popDetail.oracle.MAXCONNCOUNT" readonly />
                        </Col>
                        <Col span="8">
                            <label>当前连接数：</label><input :value="popDetail.oracle.CURRCONNCOUNT" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label>数据库状态：</label><input :value="popDetail.oracle.DBSTATE" readonly />
                        </Col>
                        <Col span="8">
                            <label>共享池容量：</label><input :value="popDetail.oracle.SHAREPOOLSIZE" readonly />
                        </Col>
                        <Col span="8">
                            <label>共享池使用率：</label><input :value="popDetail.oracle.SPUSEDPENCENT" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label>SGA命中率：</label><input :value="popDetail.oracle.SGAHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA共享缓冲区命中率：</label><input :value="popDetail.oracle.LIBRARYCACHEHITRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>SGA字典缓冲区命中率：</label><input :value="popDetail.oracle.ROWCACHEHITRATIO" readonly />
                        </Col>
                    </Row>
                    <Row>
                        <Col span="8">
                            <label>无效对象数：</label><input :value="popDetail.oracle.INVALIDOBJECT" readonly />
                        </Col>
                        <Col span="8">
                            <label>内存和硬盘排序比率：</label><input :value="popDetail.oracle.MEMORYDISKSORTSRATIO" readonly />
                        </Col>
                        <Col span="8">
                            <label>状态更新时间：</label><input :value="popDetail.oracle.UPDATETIME" readonly />
                        </Col>
                    </Row>
                </TabPane>
            </Tabs>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
            </div>
        </Drawer>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            tabKey: 'key0',
            popDetail: {detail: {},oracle: {}},
            drawerShow: false,
            popResource: false,
            popEdition: false,
            popSubsystem: false,
            popOracle: false,
            popInfo: false,
            spinShow: true,
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            statusList: [
                {
                    value: '%',
                    label: '全部'
                },
                {
                    value: '1',
                    label: '正常'
                },
                {
                    value: '2',
                    label: '异常'
                },
                {
                    value: '3',
                    label: '签退'
                },
                {
                    value: '0',
                    label: '未知'
                }
            ],
            selectStatus: '%',
            columnsResource: [
                {
                    title: '进程名',
                    key: 'key_0'
                },
                {
                    title: '进程号',
                    key: 'key_1'
                },
                {
                    title: '父进程号',
                    key: 'key_2'
                },
                {
                    title: 'CPU使用率',
                    key: 'key_3'
                },
                {
                    title: '内存使用率',
                    key: 'key_4'
                },
                {
                    title: '用户',
                    key: 'key_5'
                }
            ],
            columnsEdition: [
                {
                    title: '进程名',
                    key: 'key_0'
                },
                {
                    title: '状态',
                    key: 'key_1'
                },
                {
                    title: '端口',
                    key: 'key_2'
                },
                {
                    title: '版本',
                    key: 'key_3'
                }
            ],
            columnsInfo: [
                {
                    title: '键值',
                    key: 'key_0'
                },
                {
                    title: '队列 ID',
                    key: 'key_1'
                },
                {
                    title: '用户名',
                    key: 'key_2'
                },
                {
                    title: '权限',
                    key: 'key_3'
                },
                {
                    title: '已用字节数',
                    key: 'key_4'
                },
                {
                    title: '消息队列数',
                    key: 'key_5'
                },
            ],
            columnsSubsystem: [
                {
                    title: '子系统名称',
                    key: 'key_0'
                },
                {
                    title: '是否连接',
                    key: 'key_1'
                }
            ],
            columns: [
                {
                    title: '终端名称',
                    key: 'TERMNAME',
                    render (h, params) {
                        return h('div',params.row.TERMNAME)
                    }
                },
                {
                    title: '监控编号',
                    key: 'TERMNO'
                },
                {
                    title: '状态',
                    key: 'RUNSTATUS',
                    render: (h, params) => {
                        let color = params.row.RUNSTATUS == 1 ? 'green' : params.row.RUNSTATUS == 2 ? 'red' : params.row.RUNSTATUS == 3 ? 'blue' : 'gray'
                        let text = params.row.RUNSTATUS == 1 ? '正常' : params.row.RUNSTATUS == 2 ? '异常' : params.row.RUNSTATUS == 3 ? '签退' : '未知'
                        return h('tag',{
                            props: {
                                color: color
                            }
                        },text)
                    }
                },
                {
                    title: '更新时间',
                    key: 'EXCEPTIONSTART'
                },
                {
                    title: '扫描时间',
                    key: 'ROLLCALLTIME'
                },
                {
                    title: 'IP地址',
                    key: 'TERMIP'
                },
                {
                    title: '服务端口',
                    key: 'TERMPORT'
                },
                {
                    title: '节点编号',
                    key: 'NODENUM'
                },
                {
                    title: '终端类型',
                    key: 'TERMTYPE'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 80,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.show(params.index,self.showList[params.index].ALERTTYPECODE)
                                    }
                                }
                            },'查看')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        show (index,type) {
            const _this = this
            _this.spinShow = true
            _this.$ajax.get("json.json",{
                params: {
                    no: _this.showList[index].TERMNO
                }
            })
            .then(res => {
                _this.popDetail = res.data
                _this.tabKey = 'key0'
                const popShow = document.getElementsByClassName("pop-show")
                for (let i=0;i<popShow.length;i++) {
                    popShow[i].style.display="none"
                }
                switch(type) {
                    //其他
                    case '0':
                        document.getElementsByClassName("pop-show-0")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //钱包服务
                    case '1':
                        document.getElementsByClassName("pop-show-1")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //身份服务
                    case '2':
                        document.getElementsByClassName("pop-show-2")[0].style.display="block"
                        _this.popResource = true
                        _this.popEdition = true
                        _this.popInfo = true
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //转帐服务
                    case '3':
                        document.getElementsByClassName("pop-show-3")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //综合前置机
                    case '4':
                        document.getElementsByClassName("pop-show-4")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //数据库
                    case '5':
                        document.getElementsByClassName("pop-show-5")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = true
                        break
                    //商务网关
                    case '6':
                        document.getElementsByClassName("pop-show-6")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = true
                        _this.popOracle = false
                        break
                    //POS
                    case '7':
                        document.getElementsByClassName("pop-show-7")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //第三方子系统
                    case '8':
                        document.getElementsByClassName("pop-show-8")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //圈存机
                    case '9':
                        document.getElementsByClassName("pop-show-9")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //补卡机
                    case '10':
                        document.getElementsByClassName("pop-show-10")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //充值机
                    case '11':
                        document.getElementsByClassName("pop-show-11")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    //一体机
                    case '12':
                        document.getElementsByClassName("pop-show-12")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                    default:
                        document.getElementsByClassName("pop-show-0")[0].style.display="block"
                        _this.popResource = false
                        _this.popEdition = false
                        _this.popInfo = false
                        _this.popSubsystem = false
                        _this.popOracle = false
                        break
                }
                this.spinShow = false
                this.drawerShow = true
            })
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.selectStatus != '%') {
                this.searchStatus()
            }
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        searchStatus () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].RUNSTATUS == this.selectStatus) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        this.$ajax.get("Sys.json")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



