<style scoped>
.layout{
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.layout-logo {
  position: absolute;
  top: 20px;
  left: 25px;
  width: 140px;
  height: 33px;
  background: url(../images/logo.png) 0 0 no-repeat;
}
.layout-nav{
  width: 420px;
  margin: 0 auto;
  margin-right: 20px;
}
.ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title {
  padding: 8px 24px;
}
.portrait-poptip {
  position: absolute;
  top: 0;
  right: 0;
}
.portrait-button {
  border: none;
  padding-right: 50px;
  margin-right: 24px;
  height: 64px;
  background: url(../images/portrait.png) right 14px no-repeat;
}
.layout-copy {
  height: 24px;
  margin-top: -24px;
}
.modify-password,
.logout {
  margin-left: 15px;
  height: 30px;
  cursor: pointer;
  font-size: 14px;
}
.modify-password {
  margin: 10px 0 10px 15px;
}
.modify-password:hover,
.logout:hover {
  color: #3498db;
}
</style>
<template>
<div class="layout">
    <Layout>
        <Header>
            <div class="layout-logo"></div>
            <template>
                <Poptip class="portrait-poptip" trigger="hover" placement="bottom" width="130">
                    <i-button class="portrait-button" type="ghost">admin</i-button>
                    <div class="header-box" slot="content">
                        <div class="logout" style="margin-top: 10px;" @click="logout"><Icon type="md-log-out" size="20" style="margin-right: 5px;"></Icon>登出系统</div>
                    </div>
                </Poptip>
            </template>
        </Header>
        <Layout>
            <Sider hide-trigger :style="{background: '#515a6e'}">
                <Menu :open-names="shop2" :active-name="shop" theme="dark" width="auto" accordion="true" :style="{paddingTop: '10px'}" ref="side_menu">
                    <Submenu name="a1">
                        <template slot="title">
                            <Icon type="md-desktop"></Icon>
                            系统运行状态
                        </template>
                        <MenuItem name="overview" router-link to="/overview" replace><Icon type="md-arrow-dropright"></Icon>状态总览</MenuItem>
                        <MenuItem name="abnormal" router-link to="/abnormal" replace><Icon type="md-arrow-dropright"></Icon>异常总览</MenuItem>
                        <MenuItem name="overall" router-link to="/overall" replace><Icon type="md-arrow-dropright"></Icon>全局信息</MenuItem>
                        <MenuItem name="smap" router-link to="/smap" replace><Icon type="md-arrow-dropright"></Icon>电子地图</MenuItem>
                    </Submenu>
                    
                    <Submenu name="a3">
                        <template slot="title">
                        <Icon type="md-pie"></Icon>
                            统计与分析
                        </template>
                        <MenuItem name="abnormal-count-year" router-link to="/abnormal-count-year" replace><Icon type="md-arrow-dropright"></Icon>年度异常分析</MenuItem>
                    </Submenu>
                    
                    <Submenu name="a4">
                        <template slot="title">
                        <Icon type="md-filing"></Icon>
                            基础数据维护
                        </template>
                        <MenuItem name="contacts" router-link to="/contacts" replace><Icon type="md-arrow-dropright"></Icon>联系人</MenuItem>
                    </Submenu>
                </Menu>
            </Sider>
            <Layout :style="{padding: '24px 24px 32px'}">
                <Content id="content" :style="{padding: '24px', height: webHeight + 'px', background: '#fff', overflow: 'auto', position: 'relative'}">
                    <router-view></router-view>
                </Content>
            </Layout>
        </Layout>
    </Layout>
    <div class="layout-copy" :style="{textAlign: 'center'}">
        Copyright &copy;1988-{{ thisYear }} Harbin Synjones Electronics Co., Ltd. All Rights Reserved
    </div>
</div>
</template>
<script>
export default {
    data () {
        return {
            realHeight: window.innerHeight - 2,
            webHeight: window.innerHeight - 122,
            shop: '',
            shop2: '',
            thisYear: ''
        }
    },
    mounted () {
        const _this = this
        _this.thisYear = new Date().getFullYear()
        window.onresize = () => {
            return (() => {
                window.webHeight = window.innerHeight - 122
                _this.webHeight = window.webHeight,
                _this.realHeight = window.innerHeight
            })()
        }
        this.shop = this.$route.path.slice(1)
        switch (this.shop) {
            case 'overview': 
            case 'abnormal':
            case 'server':
            case 'system':
            case 'overall':
            case 'smap':
                this.shop2 = 'a1'
                break
            case 'operation-log': 
            case 'identity-log':
            case 'terminal-abnormal':
            case 'terminal-warning':
            case 'suspicious-pos':
            case 'suspicious-qcj':
                this.shop2 = 'a2'
                break
            case 'user-count': 
            break;
            case 'abnormal-count-termina':
            case 'abnormal-count-type':
            case 'abnormal-count-year':
            case 'abnormal-count-year':
            case 'suspicious-count-type':
            case 'operation-daily-count':
            case 'qcj-count-termina':
                this.shop2 = 'a3'
                break
            case 'contacts': 
            case 'unit':
            case 'campus':
            case 'area':
            case 'warning-rule':
            case 'abnormal-rule':
            case 'suspicious-rule':
                this.shop2 = 'a4'
                break
            case 'terminal-type': 
            case 'terminal':
            case 'data-base':
            case 'user':
                this.shop2 = 'a5'
                break
        }
        this.$nextTick(()=>{
            this.$refs.side_menu.updateOpened()
            this.$refs.side_menu.updateActiveName()
        })
    },
    watch: {
        webHeight (val) {
            if (!this.timer) {
                this.webHeight = val
                this.timer = true
                let that = this
                setTimeout(function () {
                    console.log(that.screenWidth)
                    that.init()
                    that.timer = false
                }, 400)
            }
        }
    },
    methods: {
        logout () {
            $cookies.remove('status')
            this.$router.push({path: '/'})
        }
    }
}
</script>



