<template>
    <div>
        <div class="search-con">
            <span>日期：</span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="请选择日期" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <span>告警状态：</span>
            <i-select v-model="selectStatus" style="width: 150px;margin-right: 8px;" placeholder="请选择告警状态" clearable>
                <i-option value="0">失败</i-option>
                <i-option value="1">成功</i-option>
            </i-select>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="终端告警记录表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            spinShow: true,
            self: this,
            dateValue: '',
            drawer: false,
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            selectStatus: '',
            columns: [
                {
                    title: '监控编号',
                    key: 'TERMNO'
                },
                {
                    title: '异常说明',
                    key: 'EXCEPTIONDESC'
                },
                {
                    title: '异常状态',
                    key: 'EXCEPTIONSTATE',
                    render: (h, params) => {
                        return h('div',params.row.EXCEPTIONSTATE == 1 ? '报警中' : params.row.EXCEPTIONSTATE == 2 ? '报警完毕' : params.row.EXCEPTIONSTATE == 3 ? '已处理' : '未处理')
                    }
                },
                {
                    title: '发生时间',
                    key: 'EXCEPTIONSTART'
                },
                {
                    title: '告警时间',
                    key: 'ALARMTIME'
                },
                {
                    title: '解决时间',
                    key: 'EXCEPTIONEND'
                },
                {
                    title: '告警方式',
                    key: 'ALARMMODE',
                    render: (h, params) => {
                        return h('div',params.row.ALARMMODE == 1 ? '短信' : params.row.ALARMMODE == 2 ? '邮件' : params.row.ALARMMODE == 3 ? '电话' : '无')
                    }
                },
                {
                    title: '告警状态',
                    key: 'ALARMSTUTAS',
                    render: (h, params) => {
                        return h('div',params.row.ALARMSTUTAS == 1 ? '成功' : '失败')
                    }
                },
                {
                    title: '备注信息',
                    key: 'ALARMMEMO'
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now();
                }
            }
        }
    },
    methods: {
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        ajaxData (paramsData) {
            const selfError = this
            this.spinShow = true
            this.$ajax.get("getAlarmLogNew.do",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data
                    this.searchList = this.totalList
                    this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        },
        tableSearch () {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]),
                enddate: this.padDate(this.dateValue[1]),
                alarmstatus: this.selectStatus
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        const today = new Date()
        const lastSevenDay = new Date(today.getTime() - 6 * 24 * 3600 * 1000)
        this.dateValue = [lastSevenDay,today]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1])
        }
        this.ajaxData(paramsData)
    }
}
</script>



