import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/router/Login'
import Main from '@/router/Main'

import Overview from '@/router/Overview'
import Abnormal from '@/router/Abnormal'
import Overall from '@/router/Overall'
import ServerTable from '@/router/ServerTable'
import SystemTable from '@/router/SystemTable'
import EquipmentTable from '@/router/EquipmentTable'
import SMap from '@/router/SMap'

import OperationLog from '@/router/OperationLog'
import IdentityLog from '@/router/IdentityLog'
import TerminalAbnormal from '@/router/TerminalAbnormal'
import TerminalWarning from '@/router/TerminalWarning'
import SuspiciousPos from '@/router/SuspiciousPos'
import SuspiciousQcj from '@/router/SuspiciousQcj'

import UserCount from '@/router/UserCount'
import AbnormalCountTermina from '@/router/AbnormalCountTermina'
import AbnormalCountType from '@/router/AbnormalCountType'
import AbnormalCountYear from '@/router/AbnormalCountYear'
import SuspiciousCountType from '@/router/SuspiciousCountType'
import OperationDailyCount from '@/router/OperationDailyCount'
import QcjCountTermina from '@/router/QcjCountTermina'

import Contacts from '@/router/Contacts'
import Unit from '@/router/Unit'
import Campus from '@/router/Campus'
import Area from '@/router/Area'
import WarningRule from '@/router/WarningRule'
import AbnormalRule from '@/router/AbnormalRule'
import SuspiciousRule from '@/router/SuspiciousRule'

import TerminalType from '@/router/TerminalType'
import Terminal from '@/router/Terminal'
import DataBase from '@/router/DataBase'
import User from '@/router/User'


Vue.use(Router)

export default new Router({
	routes: [
		{	
			path: '/',
			redirect: '/login'
		},
		{
			path: '/login',
			name: 'Login',
			components: {
		        login: Login
		    }
		},
		{
			path: '',
			name: 'Main',
			components: {
		        main: Main
		    },
		    children: [
				{	//1
					path: 'overview',
					name: 'Overview',
					component: Overview
				},
				{	
					path: 'abnormal',
					name: 'Abnormal',
					component: Abnormal
				},
				{	
					path: 'overall',
					name: 'Overall',
					component: Overall
				},
				{	
					path: 'server',
					name: 'ServerTable',
					component: ServerTable
				},
				{	
					path: 'system',
					name: 'SystemTable',
					component: SystemTable
				},
				{
					path: 'equipment',
					name: 'EquipmentTable',
					component: EquipmentTable
				},
				{
					path: 'smap',
					name: 'SMap',
					component: SMap
				},
				{	//2
					path: 'operation-log',
					name: 'OperationLog',
					component: OperationLog
				},
				{
					path: 'identity-log',
					name: 'IdentityLog',
					component: IdentityLog
				},
				{
					path: 'terminal-abnormal',
					name: 'TerminalAbnormal',
					component: TerminalAbnormal
				},
				{
					path: 'terminal-warning',
					name: 'TerminalWarning',
					component: TerminalWarning
				},
				{
					path: 'suspicious-pos',
					name: 'SuspiciousPos',
					component: SuspiciousPos
				},
				{
					path: 'suspicious-qcj',
					name: 'SuspiciousQcj',
					component: SuspiciousQcj
				},
				{	//3
					path: 'user-count',
					name: 'UserCount',
					component: UserCount
				},
				{	
					path: 'abnormal-count-termina',
					name: 'AbnormalCountTermina',
					component: AbnormalCountTermina
				},
				{	
					path: 'abnormal-count-type',
					name: 'AbnormalCountType',
					component: AbnormalCountType
				},
				{	
					path: 'abnormal-count-year',
					name: 'AbnormalCountYear',
					component: AbnormalCountYear
				},
				{	
					path: 'operation-daily-count',
					name: 'OperationDailyCount',
					component: OperationDailyCount
				},
				{	
					path: 'suspicious-count-type',
					name: 'SuspiciousCountType',
					component: SuspiciousCountType
				},
				{	
					path: 'qcj-count-termina',
					name: 'QcjCountTermina',
					component: QcjCountTermina
				},
				{	//4
					path: 'contacts',
					name: 'Contacts',
					component: Contacts
				},
				{	
					path: 'unit',
					name: 'Unit',
					component: Unit
				},
				{	
					path: 'campus',
					name: 'Campus',
					component: Campus
				},
				{	
					path: 'area',
					name: 'Area',
					component: Area
				},
				{	
					path: 'warning-rule',
					name: 'WarningRule',
					component: WarningRule
				},
				{	
					path: 'abnormal-rule',
					name: 'AbnormalRule',
					component: AbnormalRule
				},
				{	
					path: 'suspicious-rule',
					name: 'SuspiciousRule',
					component: SuspiciousRule
				},
				{
					//5
					path: 'terminal-type',
					name: 'TerminalType',
					component: TerminalType
					
				},
				{	
					path: 'terminal',
					name: 'Terminal',
					component: Terminal
				},
				{	
					path: 'data-base',
					name: 'DataBase',
					component: DataBase
				},
				{	
					path: 'user',
					name: 'User',
					component: User
				}
		     ]
		}
	]
})



