<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="margin-left: 8px;float: right;">添加</i-button>
            <span>终端名称：</span>
            <i-input v-model="nameValue" placeholder="请输入终端名称" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="数据库配置表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="终端：" prop="TERMNO">
                            <Select v-model="formData.TERMNO" placeholder="请选择终端" :disabled="!addShow">
                                <i-option v-for="item in schoolList" :value="item.TERMNO" :key="schoolL">{{ item.TERMNAME }}</i-option>
                            </Select>
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="数据库实例名：" prop="DATASOURCE">
                            <Input v-model="formData.DATASOURCE" placeholder="请输入数据库实例名" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="客户端网络实例名：" prop="NETMANAGERNAME">
                            <Input v-model="formData.NETMANAGERNAME" placeholder="请输入客户端网络实例名" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="管理员帐号：" prop="DBUSER">
                            <Input v-model="formData.DBUSER" placeholder="请输入管理员帐号" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="管理员密码：" prop="DBPASSWD">
                            <Input v-model="formData.DBPASSWD" placeholder="请输入管理员密码" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="EM端口：" prop="EMPORT">
                            <Input v-model="formData.EMPORT" placeholder="请输入EM端口" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="24">
                        <FormItem label="备注：" prop="REMARKS">
                            <Input v-model="formData.REMARKS" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-show="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-show="!addShow">提交</Button>
            </div>
        </Drawer>
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        const validateNum = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            contactsList: [],
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                DBID: '',
                TERMNO: '',
                DATASOURCE: '',
                NETMANAGERNAME: '',
                DBUSER: '',
                DBPASSWD: '',
                EMPORT: '',
                REMARKS: ''
            },
            ruleValidate: {
                TERMNO: [
                    { required: true, message: '不能为空', trigger: 'blur' }
                ],
                DATASOURCE: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                NETMANAGERNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                DBUSER: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                DBPASSWD: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                EMPORT: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 10,message: '不能大于10个字符', trigger: 'blur' },
                    { validator: validateNum, trigger: 'blur' }
                ],
                REMARKS: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '终端名称',
                    key: 'TERMNAME'
                },
                {
                    title: '数据库实例名',
                    key: 'DATASOURCE'
                },
                {
                    title: '客户端网络实例名',
                    key: 'NETMANAGERNAME'
                },
                {
                    title: '管理员帐号',
                    key: 'DBUSER'
                },
                {
                    title: 'EM端口',
                    key: 'EMPORT'
                },
                {
                    title: '备注',
                    key: 'REMARKS'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改'),
                            h('i-button',{
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.removeConfirm(params.index)
                                    }
                                }
                            },'删除')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.DATASOURCE
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            this.drawerShow = true
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("addDbInfoNew.do",{
                            params: {
                                TERMNO: this.formData.TERMNO,
                                DATASOURCE: this.formData.DATASOURCE,
                                NETMANAGERNAME: this.formData.NETMANAGERNAME,
                                DBUSER: this.formData.DBUSER,
                                DBPASSWD: this.formData.DBPASSWD,
                                EMPORT: this.formData.EMPORT,
                                REMARKS: this.formData.REMARKS
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listDbInfoNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("updateDbInfoNew.do",{
                            params: {
                                DBID: this.formData.DBID,
                                TERMNO: this.formData.TERMNO,
                                DATASOURCE: this.formData.DATASOURCE,
                                NETMANAGERNAME: this.formData.NETMANAGERNAME,
                                DBUSER: this.formData.DBUSER,
                                DBPASSWD: this.formData.DBPASSWD,
                                EMPORT: this.formData.EMPORT,
                                REMARKS: this.formData.REMARKS
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listDbInfoNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.DBID = this.showList[index].DBID
        },
        remove () {
            this.delShow = false
            this.$ajax.get("removeDbInfoNew.do",{
                    params: {
                        DBID: this.formData.DBID
                    }
                })
                .then(res => {
                    if (res.data.success == 'true') {
                        this.drawerShow = false
                        this.$Message.success('删除成功')
                        this.spinShow = true
                        this.$ajax.get("listDbInfoNew.do")
                            .then(res => {
                                this.totalList = res.data
                                this.searchList = this.totalList
                                this.tableLoad()
                                this.spinShow = false
                            })
                    } else {
                        this.$Message.error(res.data.msg)
                    }
                })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].TERMNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        this.$ajax.get("listDbInfoNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
        this.$ajax.get("getTerminalListNew.do",{
                params: {
                    TERMTYPECODE: 22
                }
            })
            .then(res => {
                this.schoolList = res.data
            })
    }
}
</script>



