<template>
    <div>
        <div class="search-con">
            <span>日期：</span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="请选择日期" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <span>操作员：</span>
            <i-select v-model="selectOperator" style="width: 150px;margin-right: 8px;" placeholder="请选择操作员">
                <i-option value="%" :key="operatorL">全部</i-option>
                <i-option v-for="item in operatorList" :value="item.OPERCODE" :key="operatorL">{{ item.OPERNAME }}</i-option>
            </i-select>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="人工业务统计表" icon="md-browsers">
                <i-table border stripe :content="self" :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            spinShow: true,
            dateValue: [],
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            operatorList: [],
            selectOperator: '',
            columns: [
                {
                    title: '操作员',
                    key: 'opername'
                },
                {
                    title: '日期',
                    key: 'logdate'
                },
                {
                    title: '充值',
                    align: 'center',
                    children: [
                        {
                            title: '总笔数',
                            key: 'fillnum'
                        },
                        {
                            title: '总金额',
                            key: 'fillamt',
                            render: (h, params) => {
                                let value = params.row.fillamt + ''
                                value = value.replace(/-/g, "")
                                if (value == 'null' || value == 0 || value == '0') {
                                    value = "0.00"
                                } else {
                                    let v = "" + value;
                                    if (v.indexOf(".") != -1) {
                                        value = v
                                    } else {
                                        for (let i=v.length;i<3;i++) {
                                            v = 0 + v
                                        }
                                        value = v.substring(0, v.length - 2) + "." + v.substring(v.length - 2);
                                    }
                                }
                                return h('div',value)
                            }
                        }
                    ]
                },
                {
                    title: '新开户数',
                    key: 'newnum'
                },
                {
                    title: '挂失解挂笔数',
                    align: 'center',
                    children: [
                        {
                            title: '挂失',
                            key: 'lossnum'
                        },
                        {
                            title: '解挂',
                            key: 'cancelnum'
                        }
                    ]
                },
                {
                    title: '商户结算',
                    align: 'center',
                    children: [
                        {
                            title: '清算笔数',
                            key: 'mernum'
                        },
                        {
                            title: '清算金额(元)',
                            key: 'meramt',
                            render: (h, params) => {
                                let value = params.row.meramt + ''
                                value = value.replace(/-/g, "")
                                if (value == 'null' || value == 0 || value == '0') {
                                    value = "0.00"
                                } else {
                                    let v = "" + value;
                                    if (v.indexOf(".") != -1) {
                                        value = v
                                    } else {
                                        for (let i=v.length;i<3;i++) {
                                            v = 0 + v
                                        }
                                        value = v.substring(0, v.length - 2) + "." + v.substring(v.length - 2);
                                    }
                                }
                                return h('div',value)
                            }
                        },
                        {
                            title: '退款笔数',
                            key: 'mernum2'
                        },
                        {
                            title: '退款金额',
                            key: 'meramt2',
                            render: (h, params) => {
                                let value = params.row.meramt2 + ''
                                value = value.replace(/-/g, "")
                                if (value == 'null' || value == 0 || value == '0') {
                                    value = "0.00"
                                } else {
                                    let v = "" + value;
                                    if (v.indexOf(".") != -1) {
                                        value = v
                                    } else {
                                        for (let i=v.length;i<3;i++) {
                                            v = 0 + v
                                        }
                                        value = v.substring(0, v.length - 2) + "." + v.substring(v.length - 2)
                                    }
                                }
                                return h('div',value)
                            }
                        }
                    ]
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now() - 86400000;
                }
            }
        }
    },
    methods: {
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        tableSearch () {
            // const start = new Date(this.dateValue[0])
            // const end = new Date(this.dateValue[1])
            // if ((end - start) / (1000 * 60 * 60 * 24) >= 30) {
            //     this.$Message.error ('最多查询30天');
            // } else {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]),
                enddate: this.padDate(this.dateValue[1]),
                operator: this.selectOperator
            }
            this.ajaxData(paramsData)
            // }
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        },
        ajaxData (paramsData) {
            const selfError = this;
            this.spinShow = true
            this.$ajax.get("integrateDaywork.do",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data.data
                    this.searchList = this.totalList
                    this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        }
    },
    mounted() {
        let today = new Date()
        let lastOneDay = new Date(today.getTime() - 1 * 24 * 3600 * 1000)
        let lastSevenDay = new Date(today.getTime() - 6 * 24 * 3600 * 1000)
        this.dateValue = [lastSevenDay,lastOneDay]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1])
        }
        this.ajaxData(paramsData)
        this.$ajax.get("newgetIntegrateOper.do")
            .then(res => {
                this.operatorList = res.data
            })
    }
}
</script>



