<style scoped>
.big-wrap {
    top: 0;
    left: 0;
    background-color: #252d3a;
}
.wrap {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 230px;
  margin-top: -140px;
  background-color: #e7ebee;
  box-shadow: 5px 5px 5px #000;
  font-size: 14px;
}
.content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 290px;
  height: 300px;
  margin: -150px 0 0 0;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 5px 5px 5px #000;
}
.logo {
  position: absolute;
  top: 130px;
  left: -220px;
  width: 290px;
  height: 300px;
  margin: 0;
  border-radius: 5px;
  background: url(../images/login-logo.png) 0 0 no-repeat;
}
.con {
  margin-top: 25px;
}
h1 {
  margin-top: 28px;
  text-align: center;
  font-size: 22px;
  color: #e4414b;
}
label {
    display: inline-block;
    width: 60px;
  color: #444;
  font-size: 14px;
}
a {
  position: absolute;
  bottom: 50px;
  right: -7px;
  width: 200px;
  height: 39px;
  padding-top: 9px;
  background-color: #2980b9;
  color: #fff;
  text-align: center;
  box-shadow: 3px 3px 3px #000;
  text-decoration: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
a:hover {
  box-shadow: 4px 4px 4px #000;
  background-color: #3498db;
}
a:active {
  right: -8px;
}
</style>
<template>
    <div class="big-wrap" :style="{height: webHeight + 'px'}">
        <div class="wrap">
            <div class="content">
                <div class="logo"></div>
                <h1>监控平台</h1>
                <div class="con">
                    <Form ref="formData" :model="formData" :rules="ruleValidate">
                        <Form-item prop="user" style="width: 260px;padding-left: 30px;">
                            <i-input type="text" v-model="formData.user" placeholder="请输入用户名">
                                <Icon type="md-person" slot="prepend"></Icon>
                            </i-input>
                        </Form-item>
                        <Form-item prop="password" style="width: 260px;padding-left: 30px;">
                            <i-input type="password" v-model="formData.password" placeholder="请输入密码" @keyup.native.enter="handleSubmit('formData')">
                                <Icon type="md-key" slot="prepend"></Icon>
                            </i-input>
                        </Form-item>
                            <a @click="handleSubmit('formData')">{{ submitVal }}</a>
                    </Form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        return {
            submitVal: '登　录',
            webHeight: window.innerHeight,
            formData: {
                user: '',
                password: ''
            },
            ruleValidate: {
                user: [
                    { required: true, message: '请输入帐号', trigger: 'blur' },
                    { max: 20,message: '帐号长度不能大于20位', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { type: 'string', max: 20, message: '密码长度不能大于20位', trigger: 'blur' }
                ]
            }
        }
    },
    mounted () {
        const _this = this
        window.onresize = () => {
            return (() => {
                window.webHeight = window.innerHeight
                _this.webHeight = window.webHeight
            })()
        }
    },
    methods: {
    //     handleSubmit(name) {
    //         this.$refs[name].validate((valid) => {
    //             if (valid) {
    //                 this.submitVal = '登录中,请稍候...'
    //                 this.$ajax.get("login.do",{
    //                     params: {
    //                         username: this.formData.user,
    //                         userpwd: this.formData.password,
    //                     }
    //                 })
    //                 .then(res => {
    //                     if (res.data.msg == 'success') {
    //                         this.submitVal = '登　录'
    //                         $cookies.set('status', 'logined', 120 * 60)
    //                         this.$router.push({path: 'overview'})
    //                         this.$Message.success('登录成功')
    //                     } else {
    //                         this.submitVal = '登　录'
    //                         this.$Message.error('登录信息有误')
    //                     }
    //                 })
    //             } else {
    //                 this.$Message.error('表单验证失败!')
    //             }
    //         })
    //     }
    // },
    handleSubmit(name) {
          if (this.formData.user == "admin" && this.formData.password == "admin") {
            this.submitVal = '登　录'
            $cookies.set('status', 'logined', 120 * 60)
            this.$router.push({path: 'overview'})
            this.$Message.success('登录成功') 
          } else {
            this.submitVal = '登　录'
            this.$Message.error('登录信息有误')
          }
                    
        }
    },
    watch: {
        webHeight (val) {
            if (!this.timer) {
                this.webHeight = val
                this.timer = true
                let that = this
                setTimeout(function () {
                    console.log(that.screenWidth)
                    that.init()
                    that.timer = false
                }, 400)
            }
        }
    }
}
</script>



