<template>
    <div>
        <div class="search-con">
            <span>发生日期：</span>
            <date-picker v-model="dateValue" :options="dataOptions" type="daterange" format="yyyy-MM-dd" placeholder="请选择发生日期" style="width: 200px;margin-right: 8px;" placement="bottom-end" :editable="false"></date-picker>
            <span>终端类型：</span>
            <i-select v-model="selectTerminal" style="width: 150px;margin-right: 8px;" placeholder="请选择终端类型" clearable>
                <i-option v-for="item in terminalList" :value="item.TERMTYPECODE" :key="terminalL">{{ item.TERMTYPENAME }}</i-option>
            </i-select>
            <span>异常状态：</span>
            <i-select v-model="selectStatus" style="width: 150px;margin-right: 8px;" placeholder="请选择异常状态" clearable>
                <i-option value="0">未处理</i-option>
                <i-option value="1">报警中</i-option>
                <i-option value="2">报警完毕</i-option>
                <i-option value="3">已处理</i-option>
            </i-select>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="终端异常记录表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            title="告警记录"
            v-model="drawer"
            width="1000"
            :mask-closable="true"
            :styles="styles"
        >
            <i-table border stripe :columns="columns2" :data="columns2List" style="min-width: 800px"></i-table>
            <div class="demo-drawer-footer">
                <Button @click="drawer = false">关闭</Button>
            </div>
        </Drawer> 
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        return {
            spinShow: true,
            self: this,
            drawer: false,
            dateValue: '',
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            terminalList: [],
            selectTerminal: '',
            selectStatus: '',
            columns: [
                {
                    title: '监控编号',
                    key: 'TERMNO'
                },
                {
                    title: '终端类型',
                    key: 'TERMTYPENAME'
                },
                {
                    title: '异常说明',
                    key: 'EXCEPTIONDESC'
                },
                {
                    title: '发生时间',
                    key: 'EXCEPTIONSTART'
                },
                {
                    title: '告警次数',
                    key: 'ALARMCOUNT'
                },
                {
                    title: '异常状态',
                    key: 'EXCEPTIONSTATE',
                    render: (h, params) => {
                        return h('div',params.row.EXCEPTIONSTATE == 1 ? '报警中' : params.row.EXCEPTIONSTATE == 2 ? '报警完毕' : params.row.EXCEPTIONSTATE == 3 ? '已处理' : '未处理')
                    }
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 180,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.show(params.index)
                                    }
                                }
                            },'查看'),
                            h('i-button',{
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.showPop(params.index)
                                    }
                                }
                            },'告警记录')
                        ])
                    }
                }
            ],
            columns2List: [],
            columns2: [
                {
                    title: '告警时间',
                    key: 'ALARMTIME'
                },
                {
                    title: '告警方式',
                    key: 'ALARMMODE',
                    render: (h, params) => {
                        return h('div',params.row.ALARMMODE == 1 ? '短信' : params.row.ALARMMODE == 2 ? '邮件' : params.row.ALARMMODE == 3 ? '电话' : '无')
                    }
                },
                {
                    title: '告警状态',
                    key: 'ALARMSTUTAS',
                    render: (h, params) => {
                        return h('div',params.row.ALARMSTUTAS == 1 ? '成功' : '失败')
                    }
                },
                {
                    title: '备注信息',
                    key: 'ALARMMEMO'
                }
            ],
            dataOptions: {
                disabledDate (date) {
                    return date && date.valueOf() > Date.now();
                }
            }
        }
    },
    methods: {
        show (index) {
            this.$Modal.info({
                title: `${this.showList[index].TERMNAME}`,
                content: `<br>异常定义：${this.showList[index].EXCDESC}<br>异常说明：${this.showList[index].EXCEPTIONDESC}<br>发生时间：${this.showList[index].EXCEPTIONSTART}<br>异常状态：${this.showList[index].EXCEPTIONSTATE == 1 ? '报警中' : this.showList[index].EXCEPTIONSTATE == 2 ? '报警完毕' : this.showList[index].EXCEPTIONSTATE == 3 ? '已处理' : '未处理'}<br>解决时间：${this.showList[index].EXCEPTIONEND == undefined ? '' : this.showList[index].EXCEPTIONEND}<br>报警次数：${this.showList[index].ALARMCOUNT == undefined ? '' : this.showList[index].ALARMCOUNT}<br>备注信息：${this.showList[index].EXCEPTIONPROCESS == undefined ? '' : this.showList[index].EXCEPTIONPROCESS}`
            })
        },
        showPop (index) {
            this.spinShow = true
            this.columns2List = []
            this.$ajax.get("static/TerminaAbnormal.json",{
                    params: {
                        exceptionid: this.showList[index].EXCEPTIONID
                    }
                })
                .then(res => {
                    this.columns2List = res.data
                    this.spinShow = false
                    this.drawer = true
                    this.spinShow = false
                })
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        padDate (date) {
            let year = date.getFullYear()
            const m = (date.getMonth() + 1)
            let month = m < 10 ? '0' + m : m
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
            return year + '-' + month + '-' + day
        },
        ajaxData (paramsData) {
            const selfError = this
            this.spinShow = true
            this.$ajax.get("static/TerminaAbnormal.json",{
                    params: paramsData
                })
                .then(res => {
                    this.totalList = res.data
                    this.searchList = this.totalList
                    this.tableLoad()
                    this.spinShow = false
                })
                .catch(function (error) {
                    const str = error + ''
                    if (str.search('timeout') !== -1) {
                        selfError.$Message.error('请求超时')
                        selfError.spinShow = false
                    }
                })
        },
        tableSearch () {
            const paramsData = {
                startdate: this.padDate(this.dateValue[0]),
                enddate: this.padDate(this.dateValue[1]),
                termtypecode: this.selectTerminal,
                exceptionstate: this.selectStatus
            }
            this.ajaxData(paramsData)
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        const today = new Date()
        const lastSevenDay = new Date(today.getTime() - 6 * 24 * 3600 * 1000)
        this.dateValue = [lastSevenDay,today]
        const paramsData = {
            startdate: this.padDate(this.dateValue[0]),
            enddate: this.padDate(this.dateValue[1])
        }
        this.ajaxData(paramsData)
        this.$ajax.get("static/TerminaAbnormal.json")
            .then(res => {
                this.terminalList = res.data
            })
    }
}
</script>



