<template>
    <div>
        <div class="search-con">
            <i-button @click="add" type="primary" icon="md-add" style="margin-left: 8px;float: right;">添加</i-button>
            <span>名称：</span>
            <i-input v-model="nameValue" placeholder="请输入名称" style="width: 150px;margin-right: 8px;"></i-input>
            <i-button @click="tableSearch" type="primary" icon="md-search">查询</i-button>
        </div>
        <Tabs>
            <TabPane label="校区表" icon="md-browsers">
                <i-table border stripe :columns="columns" :data="showList" style="min-width: 800px"></i-table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="dataTotal" :current="pageCurrent" @update:current="val => pageCurrent = val" :page-size="pageSize" @on-change="changePage" show-total show-elevator></Page>
                    </div>
                </div>
            </TabPane>
        </Tabs>
        <Drawer
            :title="this.drawTitle"
            v-model="drawerShow"
            width="560"
            :mask-closable="true"
            :styles="styles"
        >
            <Form ref="formData" :model="formData" :rules="ruleValidate">
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="编码：" prop="CAMPUSCODE">
                            <Input v-model="formData.CAMPUSCODE" placeholder="请输入编码" :disabled="!addShow" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="名称：" prop="CAMPUSNAME">
                            <Input v-model="formData.CAMPUSNAME" placeholder="请输入名称" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="12">
                        <FormItem label="X坐标：" prop="GRAPHYX">
                            <Input v-model="formData.GRAPHYX" placeholder="请输入X坐标" />
                        </FormItem>
                    </Col>
                    <Col span="12">
                        <FormItem label="Y坐标：" prop="GRAPHYY">
                            <Input v-model="formData.GRAPHYY" placeholder="请输入Y坐标" />
                        </FormItem>
                    </Col>
                </Row>
                <Row :gutter="16">
                    <Col span="24">
                        <FormItem label="备注：" prop="MEMO">
                            <Input v-model="formData.MEMO" type="textarea" :autosize="{minRows: 2,maxRows: 2}" placeholder="请输入备注"></Input>
                        </FormItem>
                    </Col>
                </Row>
             </Form>
             <div class="demo-drawer-footer">
                <Button type="error" style="margin-right: 8px" @click="drawerShow = false">关闭</Button>
                <Button type="success" @click="addSubmit('formData')" v-show="addShow">提交</Button>
                <Button type="success" @click="editSubmit('formData')" v-show="!addShow">提交</Button>
            </div>
        </Drawer> 
        <Modal v-model="delShow" width="280" :styles="{top: '200px'}">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>数据删除后不可恢复，是否确认删除？</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long @click="remove">删除</Button>
            </div>
        </Modal>
        <Spin fix v-if="spinShow">
            <Icon type="ios-loading" size=30 class="demo-spin-icon-load"></Icon>
            <div style="font-size: 14px;padding-top: 15px">加载中，请稍候</div>
        </Spin>
    </div>
</template>
<script>
export default {
    data () {
        const validateName = (rule, value, callback) => {
            const reg = /^[\u4E00-\u9FA5A-Za-z0-9]+$/
            if (!reg.test(value)) {
                callback(new Error('请输入汉字、字母或数字'))
            } else {
                callback()
            }
        }
        const validateNumber = (rule, value, callback) => {
            const reg = /^([1-9]\d*|[0]{1,1})$/
            if (!reg.test(value)) {
                callback(new Error('请输入整数'))
            } else {
                callback()
            }
        }
        const validateGraphy = (rule, value, callback) => {
            const reg = /^([1-9]+(\.\d+)?|0\.\d+)$/
            if (value === '') {
                callback()
            } else if (!reg.test(value)) {
                callback(new Error('请输入正确的坐标'))
            } else {
                callback()
            }
        }
        return {
            delShow: false,
            addShow: true,
            spinShow: true,
            drawerShow: false,
            drawTitle: '',
            contactsList: [],
            styles: {
                height: 'calc(100% - 55px)',
                overflow: 'auto',
                paddingBottom: '53px',
                position: 'static'
            },
            formData: {
                CAMPUSCODE: '',
                CAMPUSNAME: '',
                GRAPHYX: '',
                GRAPHYY: '',
                MEMO: ''
            },
            ruleValidate: {
                CAMPUSCODE: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 5,message: '不能大于5个字符', trigger: 'blur' },
                    { validator: validateNumber, trigger: 'blur' }
                ],
                CAMPUSNAME: [
                    { required: true, message: '不能为空', trigger: 'blur' },
                    { max: 20,message: '不能大于20个字符', trigger: 'blur' },
                    { validator: validateName, trigger: 'blur' }
                ],
                GRAPHYX: [
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' },
                    { validator: validateGraphy, trigger: 'blur' }
                ],
                GRAPHYY: [
                    { max: 30,message: '不能大于30个字符', trigger: 'blur' },
                    { validator: validateGraphy, trigger: 'blur' }
                ],
                MEMO: [
                    { max: 100,message: '不能大于100个字符', trigger: 'blur' }
                ]
            },
            totalList: [],
            showList: [],
            searchList: [],
            dataTotal: 0,
            pageCurrent: 1,
            pageSize: 10,
            nameValue: '',
            columns: [
                {
                    title: '编码',
                    key: 'CAMPUSCODE'
                },
                {
                    title: '名称',
                    key: 'CAMPUSNAME'
                },
                {
                    title: 'X坐标',
                    key: 'GRAPHYX'
                },
                {
                    title: 'Y坐标',
                    key: 'GRAPHYY'
                },
                {
                    title: '备注',
                    key: 'MEMO'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        const self = this
                        return h('div',[
                            h('i-button',{
                                props: {
                                    type: 'warning',
                                    size: 'small'
                                },
                                attrs: {
                                    style: "margin-right: 10px;"
                                },
                                on: {
                                    click: () => {
                                        self.edit(params.index)
                                    }
                                }
                            },'修改'),
                            h('i-button',{
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        self.removeConfirm(params.index)
                                    }
                                }
                            },'删除')
                        ])
                    }
                }
            ]
        }
    },
    methods: {
        add () {
            this.addShow = true
            this.drawerShow = true
            this.drawTitle = '添加'
            this.handleReset('formData')
        },
        edit (index) {
            this.addShow = false
            const _showList = this.showList[index]
            const _formData = this.formData
            this.drawTitle = '修改：' + _showList.CAMPUSNAME
            this.handleReset('formData')
            const arr = Object.keys(_formData)
            for (let i=0;i<arr.length;i++) {
                if (_showList[arr[i]] == '') {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                } else if (_showList[arr[i]] == null) {
                    _formData[arr[i]] = ''
                } else {
                    _formData[arr[i]] = _showList[arr[i]].toString()
                }
            }
            this.drawerShow = true
        },
        addSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("addCampusNew.do",{
                            params: {
                                campuscode: this.formData.CAMPUSCODE,
                                campusname: this.formData.CAMPUSNAME,
                                graphyx: this.formData.GRAPHYX,
                                graphyy: this.formData.GRAPHYY,
                                memo: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('添加成功!')
                                this.spinShow = true
                                this.$ajax.get("listCampusNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                            } else {
                                this.$Message.error(res.data.msg)
                            }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        editSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$ajax.get("editCampusNew.do",{
                            params: {
                                campuscode: this.formData.CAMPUSCODE,
                                campusname: this.formData.CAMPUSNAME,
                                graphyx: this.formData.GRAPHYX,
                                graphyy: this.formData.GRAPHYY,
                                memo: this.formData.MEMO
                            }
                        })
                        .then(res => {
                            if (res.data.success == 'true') {
                                this.drawerShow = false
                                this.$Message.success('修改成功!')
                                this.spinShow = true
                                this.$ajax.get("listCampusNew.do")
                                    .then(res => {
                                        this.totalList = res.data
                                        this.searchList = this.totalList
                                        this.tableLoad()
                                        this.spinShow = false
                                    })
                                } else {
                                    this.$Message.error(res.data.msg)
                                }
                        })
                } else {
                    this.$Message.error('表单验证失败!')
                }
            })
        },
        removeConfirm (index) {
            this.delShow = true
            this.formData.CAMPUSCODE = this.showList[index].CAMPUSCODE
        },
        remove () {
            this.delShow = false
            this.$ajax.get("removeCampusNew.do",{
                    params: {
                        campuscode: this.formData.CAMPUSCODE
                    }
                })
                .then(res => {
                    if (res.data.success == 'true') {
                        this.$Message.success('删除成功')
                        this.spinShow = true
                        this.$ajax.get("listCampusNew.do")
                            .then(res => {
                                this.totalList = res.data
                                this.searchList = this.totalList
                                this.tableLoad()
                                this.spinShow = false
                            })
                    } else {
                        this.$Message.error(res.data.msg)
                    }
                })
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        },
        changePage (index) {
            const _start = (index - 1) * this.pageSize
            const _end = index * this.pageSize
            this.showList = this.searchList.slice(_start, _end)
        },
        tableSearch () {
            this.searchList = this.totalList
            if (this.nameValue != '') {
                this.searchName()
            }
            this.tableLoad()
        },
        searchName () {
            let arr = []
            for (let i=0;i<this.searchList.length;i++) {
                if (this.searchList[i].CAMPUSNAME.indexOf(this.nameValue) >= 0) {
                    arr.push(this.searchList[i])
                }
            }
            this.searchList = arr
        },
        tableLoad () {
            this.dataTotal = this.searchList.length
            if (this.searchList < this.pageSize) {
                this.showList = this.searchList
            } else {
                this.showList = this.searchList.slice(0,this.pageSize)
            }
            this.pageCurrent = 1
            this.$emit('update:current', this.pageCurrent)
        }
    },
    mounted() {
        this.$ajax.get("listCampusNew.do")
            .then(res => {
                this.totalList = res.data
                this.searchList = this.totalList
                this.tableLoad()
                this.spinShow = false
            })
    }
}
</script>



